import SimilarCourse from "../../components/SimilarCourse/SimilarCourse";
import { useSearchParams } from "react-router-dom";
import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import { filterCourse, getAllCourse } from "../../Http";
import { useNavigate } from "react-router-dom";
import Loader from "../../components/Loader/Loader";
import axios from "axios";
import FeaturedCourse from '../../components/FeaturedCourse/FeaturedCourse';
import Footer from "../../components/Footer/Footer";
import { Pagination } from "antd";
import { toast } from 'react-toastify'
import { useSelector } from "react-redux";
import { SavedCourse } from "../../Http";

function Course () {


    const navigate = useNavigate();
    const {is_login} = useSelector(state => state.auth)

    const [courseData, setCourseData] = useState(false);
    const [page, setPage] = useState(0);
    const [totalRec, setTotalRec] = useState(0);
    const [tottalPage, setTotalPage] = useState(0);
    const [loading, setLoading] = useState(true);
    const [simalarCourse, setSimilarCourse] = useState(false);
    const { id } = useParams();
    const [compareBtn, setCompareBtn] = useState('');



    const addToCompare = (id) => {

        if(!is_login) {
            toast.error('You must be login', {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });
            return
        }

       

        let comStr = localStorage.getItem('compares');
        let compArr = JSON.parse(comStr);

        let index = compArr.findIndex((a) => a === id);

        if(index === -1){
        compArr.push(id);
        localStorage.setItem('compares', JSON.stringify(compArr));
        toast.success("Added to compare", {
            position: "bottom-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        });
        setCompareBtn('active-compare')
       
    }else{

        compArr.splice(index, 1);
        localStorage.setItem('compares', JSON.stringify(compArr));
        toast.success("Removed from compare", {
            position: "bottom-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        })
        setCompareBtn('')
        

    }

      

    }


    const save_course = async (id) => {
        

        if(!is_login) {
            toast.error('You must be login', {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });
            return
        }
       

            const obj = {
                
                "course_id": id
            }

            var info = await SavedCourse(obj)

            if (info.data) {
                toast.success(info.data.message, {
                    position: "bottom-right",
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });


            } else {
                toast.error(info.data.message, {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });

            }
    }


   

    useEffect(() => {
        const getCourse = async () => {
            const {data} = await filterCourse(`?id=${id}`);
            setCourseData(data.requireddata)
            
            if(courseData) {
                setLoading(false)
            }
            
        }
        getCourse();
    }, [id, courseData])


    useEffect(() => {
        const similar = async () => {
            const getSililarCourseData = async () => {
                let similarCourseData = await getAllCourse(page);
                console.log(similarCourseData.data)
                setSimilarCourse(similarCourseData.data.coursedata)
                setTotalPage(similarCourseData.data.totalPage);
                setTotalRec(similarCourseData.data.totalRecords);

                
             }
             getSililarCourseData();
        }
        similar()
    }, [page])

    if(loading){
        return <Loader />
    }else{
        return (
            <div className="courseWrapper w-full">
        <div className='px-6 py-4 text-xs md:text-sm'>
            <p><span className="cursor-pointer" onClick={() => navigate('/')}><i className="fa-solid fa-arrow-left px-2"></i> <span className="mx-1">Search Board</span></span> <i class="fa-solid fa-chevron-right"></i> 
            <span className="cursor-pointer" onClick={() => navigate(`/country/${courseData[0].aa_institute.university_location}`)}>  {courseData[0].aa_institute.university_location}</span> <i className="fa-solid fa-chevron-right px-2"></i> 
            <span className="cursor-pointer" onClick={() => navigate(`/country/${courseData[0].aa_institute.university_location}/${courseData[0].aa_institute._id}`)}>{courseData[0].aa_institute.university_name}</span> <i className="fa-solid fa-chevron-right px-2"></i> 
            <strong>{courseData[0].course_name}</strong></p>
        </div>
        <div className="course w-full flex">
            <div className="sidebar md:w-[280px] w-[0px] overflow-hidden ">
                <div className="universicy bg-[#DBF9FB] py-8 px-4 rounded-md w-full">
                    <div className="intro text-center">
                        <img src={`http://139.59.25.83:5000${courseData[0].aa_institute.univercity_logo}`} alt='logo' className='w-[150px] mx-auto mb-6' />
                        <div className='heading mb-6'>
                           <h1 className='text-xl font-bold text-secondary'>{courseData[0].aa_institute.university_name}</h1>
                           <span className='text-sm font-[500] text-[#7C878E] mt-[-10px]'>{courseData[0].aa_institute.university_address}</span>
                        </div>
                        <div className='map'>
                            <p className='text-[#7C878E] font-[500] text-md'><i className="fa-solid fa-map-location"></i> View Map</p>
                        </div>
                    </div>
                    <div className='info mt-12'>
                        { courseData[0].aa_institute.about_university && <h1 className='text-lg font-[500] text-secondary'>About</h1>}
                        <p className='text-[#586A78] text-sm text-center leading-[19px]'>{courseData[0].aa_institute.about_university}</p>
                    </div>
                    <div className='button mt-12 text-center'>
                        <button 
                         onClick={() => navigate(`/country/${courseData[0].aa_institute.university_location}/${courseData[0].aa_institute._id}`)}
                         className='px-6 py-2 border border-primary bg-[#ffffff] text-primary text-sm font-[500] rounded-3xl hover:bg-primary hover:text-[#ffffff] cursor-pointer'>Explore University</button>
                    </div>
                    <div className='website text-center mt-12'>
                        <a className='text-sm font-bold text-[#0A1C2B]' href={courseData[0].aa_institute.university_website}>Website</a>
                    </div>
                </div>
            </div>
            <div className="main md:pl-6 px-2 w-full">
                <div className='main-inner'>
                    <div className='heading flex flex-col md:flex-row justify-between w-full md:w-[90%]'>
                        <div className='left'>
                            <h1 className='md:text-2xl text-lg font-bold text-[#0A1C2B]'>{ courseData[0].course_name }</h1>
                            <span className='text-xs text-[#7C878E]'>{courseData[0].course_level}</span>
                        </div>
                        <div className='right flex items-center my-4 md:my-0 md:justify-start justify-between'>
                            <div className='innerleft mr-4'>
                                <button className='bg-primary text-[#ffffff] py-2 px-6 rounded-3xl'>Start Application</button>
                            </div>
                            <div className='inner-right'>
                                <span className='text-xs text-[#7C878E]'>Application Fee</span>
                                <p className='text-md text-decondary font-bold'>$: { courseData[0].application_fees }</p>
                            </div>
                        </div>
                    </div>

                    <div className='gallery flex flex-col relative md:flex-row space-4 mt-2'>
                        <div className="md:w-[200px] md:h-[100%] w-full h-[150px] bg-[#ffffff] absolute right-0 md:top-0 bottom-0 flex flex-col justify-end pb-12">
                        <button className="md:mx-8 text-sm font-semibold w-[60%] border border-[#0A1C2B] px-4 py-2 rounded-3xl"
                        id={compareBtn}
                        onClick={() => addToCompare(courseData[0]._id)}
                        ><i class="fa-solid fa-code-compare"></i> Compare</button>
                        <button className="md:mx-8 w-[60%] text-[#ffffff] bg-[#afafaf] mt-4 text-sm font-semibold border border-[#afafaf] px-4 py-2 rounded-3xl"
                        onClick={() => save_course(courseData[0]._id)}
                        ><i class="fa-solid fa-bookmark"></i> <span className="ml-2">Save</span></button>
                        </div>
                        <div className='item1 md:w-1/2 w-full md:pr-2'>
                            <img className="rounded-lg" src={courseData[0].banners ? courseData[0].banners[0] : '/assets/images/und1.png'} alt='univercity01' />
                        </div>
                        <div className='flex flex-wrap w-full md:w-1/2 mt-2 md:mt-2 md:pl-2'>
                        <div className='item1 w-1/2 pb-2 pr-2'>
                            <img className="rounded-lg" src={courseData[0].banners ? courseData[0].banners[0] : '/assets/images/und2.png'} alt='univercity01' />
                        </div>
                        <div className='item1 w-1/2 pb-2 pl-2'>
                            <img className="rounded-lg" src={courseData[0].banners ? courseData[0].banners[0] : '/assets/images/und3.png'} />
                        </div>
                        <div className='item1 w-1/2 pt-2 pr-2'>
                            <img className="rounded-lg" src={courseData[0].banners ? courseData[0].banners[0] : '/assets/images/und4.png'} alt='univercity01' />
                        </div>
                        <div className='item1 w-1/2 pt-2 pl-2'>
                            <img className="rounded-lg" src={courseData[0].banners ? courseData[0].banners[0] : '/assets/images/und5.png'} alt='univercity01' />
                        </div>
                        </div>
                    </div>

                    <div className='course-detail flex mt-8 flex-col md:flex-row'>
                        <div className='left w-[100%] md:w-[70%] md:pl-4'>
                            {courseData[0].course_summary && <h1 className='md:text-xl text-lg  font-[600]'>Course Summary</h1>}
                            <p className='text-sm mt-4'>{courseData[0].course_summary}
                            </p>
                           
                           
                            <div className='key-point flex flex-col md:flex-row justify-between items-center mt-12'>
                              <div className="flex md:w-1/2 md:pr-4 w-full">
                              <div className='btn w-1/2'>
                                  <button className='bg-primary text-[#ffffff] py-2 px-6 rounded-3xl'>Start Application</button>
                                </div>
                                <div className='icon-box flex items-center'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i class="fa-solid fa-graduation-cap"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Course Language</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{courseData[0].language}</p>
                                    </div>
                                </div>
                              </div>
                             <div className="flex md:w-1/2  md:pl-4 flex-row mt-4 md:mt-0 mb-6 md:mb-0 w-full">
                             <div className='icon-box flex items-center w-1/2'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i class="fa-solid fa-calendar-days"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Intake Months</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{courseData[0].aa_institute.intake_months[0]}</p>
                                    </div>
                                </div>
                                <div className='icon-box flex items-center'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i className="fa-solid fa-timer"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Course Type</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{courseData[0].course_type}</p>
                                    </div>
                                </div>
                             </div>
                            </div>
                        </div>
                        <div className='right md:pl-6 w-full md:w-[30%]'>
                            <h1 className='md:text-xl text-md font-[600]'>Course Highlights</h1>
                        <div className="mt-6 bg-[#E2E2E2] w-full p-6 rounded-lg">
                                <div className='icon-box flex items-center mb-2'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <img src='/assets/icons/Bachelor.svg' className='w-[17px]' alt='icon' />
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Course Level</span>
                                        <p className='text-[#0A1C2B] text-md font-bold'>{courseData[0].course_level}</p>
                                    </div>
                                </div>

                                <div className='icon-box flex items-center mb-2'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <img src='/assets/icons/Watch.svg' className='w-[17px]' alt='icon' />
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Course Duration</span>
                                        <p className='text-[#0A1C2B] text-md font-bold'>{courseData[0].course_duration + ' Weeks'}</p>
                                    </div>
                                </div>

                                <div className='icon-box flex items-center mb-2'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <img src='/assets/icons/Dollar.svg' className='w-[12px]' alt='icon' />
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Tuition Fee</span>
                                        <p className='text-[#0A1C2B] text-md font-bold'>$: {courseData[0].tutation_fees}</p>
                                    </div>
                                </div>

                                <div className='icon-box flex items-center mb-2'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <img src='/assets/icons/Home.svg' className='w-[20px]' alt='icon' />
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Estimated Total Cost</span>
                                        <p className='text-[#0A1C2B] text-md font-bold'>$: {courseData[0].estimated_total_cost}</p>
                                    </div>
                                </div>

                                <div className='icon-box flex items-center mb-2'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <img src='/assets/icons/Exc.svg' className='w-[20px]' alt='icon' />
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Application Fee</span>
                                        <p className='text-[#0A1C2B] text-md font-bold'>$: {courseData[0].application_fees}</p>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>


                    <div className='requirement flex flex-col md:flex-row justify-between mt-3'>
                        <div className='addinission w-full md:w-[30%]'>
                            <h1 className='text-md font-bold'>Admission Requirements</h1>
                            <div className='border border-[#707070] rounded-lg p-2 flex justify-between item-center w-[100%]'>
                                <div  className='border-r border-r-[#707070] w-[65%]'>
                                    <span className='text-xs text-[#7C878E]'>Minimum Level of Education</span>
                                    <p className='text-md font-bold'>{courseData[0].minimum_education ? courseData[0].minimum_education : "Null"}</p>
                                </div>
                                <div>
                                    <span className='text-xs text-[#7C878E]'>Minimum GPA</span>
                                    <p className='text-md font-bold'>{courseData[0].min_gpa ? courseData[0].min_gpa : 'Null'}</p>
                                </div>
                            </div>
                        </div>

                        <div className='addinission w-full my-2 md:my-0 md:w-[30%]'>
                            <h1 className='text-md font-bold'>English Requirements</h1>
                            <div className='border border-[#707070] rounded-lg p-2 flex justify-between item-center w-[100%]'>
                                <div  className='border-r border-r-[#707070] w-[25%]'>
                                    <span className='text-xs text-[#7C878E]'>IELTS</span>
                                    <p className='text-md font-bold'>{courseData[0].ielts_requirement ? courseData[0].ielts_requirement : 'Null'}</p>
                                </div>
                                <div className='w-[28%] border-r border-r-[#707070]'>
                                    <span className='text-xs text-[#7C878E]'>TOEFL Internet</span>
                                    <p className='text-md font-bold'>{courseData[0].toefl_requirement ? courseData[0].toefl_requirement : 'Null'}</p>
                                </div>
                                <div>
                                    <span className='text-xs text-[#7C878E]'>PTE</span>
                                    <p className='text-md font-bold'>{courseData[0].pte_requirement ? courseData[0].pte_requirement : 'Null'}</p>
                                </div>
                            </div>
                        </div>

                        
                      
                                <div className='addinission w-full md:w-[30%]'>
                            <h1 className='text-md font-bold'>Scholarship</h1>
                            <div className='border border-[#707070] rounded-lg p-2 flex justify-between item-center w-[100%]'>
                                <div  className='border-r border-r-[#707070] w-[50%]'>
                                    <span className='text-xs text-[#7C878E]'>Minimum Stipend</span>
                                    <p className='text-md font-bold'>{courseData[0].min_stipend ? courseData[0].min_stipend : 'Null'}</p>
                                </div>
                                <div>
                                    <span className='text-xs text-[#7C878E]'>Maximum Stipend</span>
                                    <p className='text-md font-bold'>{courseData[0].max_stipend ? courseData[0].max_stipend : 'Null'}</p>
                                </div>
                            </div>
                        </div>
                            
                        
                    </div>


                    <div className='Similar Course mt-10'>
                        <div className='flex items-center'>
                            <h1 className='text-lg font-bold mr-4'>Similar Course</h1>
                            <a className='text-primary underline'>View All</a>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-2 gap-3 mt-4'>
                            {
                                simalarCourse.map((sc) => <FeaturedCourse course={sc} />)
                            }
                        </div>
                        <div className="flex justify-center pt-8">
                            <Pagination total={totalRec} pageSize={6} onChange={(page, pageSize) => setPage(page)} />
                        </div>
                        
                    </div>
                    
                </div>
                
            </div>
            
        </div>
        <Footer />
    </div>
        )
    }
}

export default Course;