import { useState } from "react";
import { Link, Outlet } from "react-router-dom";
import { NavLink, useNavigate } from 'react-router-dom'
import UserAccount from "../userAccount/UserAccount";

const Navigation = () => {
  



  return (
    <div className="navigation">
      <div className="px-3 w-full sticky top-0 bg-[#ffffff] flex justify-between items-center shadow-md zzz">
        <div className='left flex h-[60px] items-center'>
          <div className='logo mr-8'>
            <img src='/assets/images/logo.png' alt="logo" className="h-[50px]" />
          </div>
          <div className='menu hidden md:block'>
            <ul className='flex items-center h-[60px]'>
              <NavLink
                to='/'
                className='h-[60px]
                          relative 
                          flex 
                          items-center 
                          mx-8 w-[120px] 
                          justify-center'

              >
                <span
                  className='text-md 
                             font-semibold 
                             text-[#0A1C2B] 
                             opacity-[.49] 
                             hover:opacity-[1] 
                             cursor-pointer nav-text'
                             

                >Search Board</span>
                <span
                  className='opacity-[1] 
                             absolute w-0 
                             h-[5px] 
                             rounded-md 
                             bg-primary 
                             flex nav-indi
                             bottom-0 duration-300'
                             

                ></span>
              </NavLink>
              <NavLink
              to='/saved-course'
              id={(isActive, isPending) => isActive ? 'active-nav' : ''}
                className='h-[60px]
                          relative 
                          flex 
                          items-center 
                          mx-8 w-[120px] 
                          justify-center'

              >
                <span
                  className='text-md 
                             font-semibold 
                             text-[#0A1C2B] 
                             opacity-[.49] 
                             hover:opacity-[1] 
                             cursor-pointer nav-text' 
                             

                >Saved Course</span>
                <span
                  className='opacity-[1] 
                             absolute w-[0] 
                             h-[5px] 
                             rounded-md 
                             bg-primary 
                             flex nav-indi
                             bottom-0 duration-300'
                             

                ></span>
              </NavLink>
              <li
                className='h-[60px]
                          relative 
                          flex 
                          items-center 
                          mx-8 w-[120px] 
                          justify-center'

              >
                <span
                  className='text-md 
                             font-semibold 
                             text-[#0A1C2B] 
                             opacity-[.49] 
                             hover:opacity-[1] 
                             cursor-pointer'

                >My Application</span>
                <span
                  className='
                             absolute w-[100%] 
                             h-[5px] 
                             rounded-md 
                             bg-primary 
                             flex 
                             bottom-0 opacity-[0]'

                ></span>
              </li>

            </ul>
          </div>
        </div>
        <div className='right'>
            <UserAccount />
        </div>
      </div>
      <Outlet />
      <div className="fixed bottom-0 left-0 right-0 mobile-nav md:hidden">
        <div className="bg-[#ffffff] grid grid-cols-3">
        
            <div className="text-[#adadad] py-4 flex flex-col items-center mob-nav-item">
             <i class="fa-regular fa-file text-2xl"></i>
              <span className="text-sm">My Application</span>
            </div>
         
          <NavLink to='/'>
            <div className="text-[#adadad] py-4 flex flex-col items-center mob-nav-item">
            <i class="fa-solid fa-magnifying-glass text-2xl w=[10px]"></i>
              <span className="text-sm">Search Board</span>
            </div>
          </NavLink>
          <NavLink to='/saved-course'>
            <div className="text-[#adadad] py-4 flex flex-col items-center mob-nav-item">
              <i class="fa-regular fa-bookmark text-2xl"></i>
              <span className="text-sm">Saved Course</span>
            </div>
          </NavLink>
        </div>
      </div>
    </div>
  )
}

export default Navigation;