import './index.css';
import { createBrowserRouter, RouterProvider } from 'react-router-dom';
import Navigation from './components/Navigation/Navigation';
import SearchBoard from './pages/SerachBoard/SearchBoard';
import Course from './pages/Course/Course';
import Country from './pages/Country/Country';
import { Children, useEffect } from 'react';
import Institutions from './pages/Country/Institutions';
import CountryCourses from './pages/Country/Coursers';
import Univercity from './pages/Univercity/Univercity';
import Explore from './pages/Explore/Explore';
import Login from './pages/Login/Login';
import Otp from './pages/otp/Otp';
import SavedCourse from './pages/Savedcourse/SavedCourse';
import Comapre from './pages/Compare/Comapre';
import {useDispatch} from 'react-redux';
import { setAuth, setPerDetdail, setStudyPref, setEducation, setEngTest, setStanderTest } from './store/authSlice';
import { setLogin } from './store/authSlice';
import axios from 'axios';
import Profile from './pages/Profile/Profile';
import Personal from './pages/Profile/Personal';
import AdmissionTest from './pages/Profile/AdmissionTest';
import Documents from './pages/Profile/Documents';
import Education from './pages/Profile/Education';
import Employement from './pages/Profile/Employement';
import EnglishTest from './pages/Profile/EnglishTest';
import { getPersonalData, getStudyPref, getEducationData, getEnglishData, getStaTest } from './Http';



import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';


const router = createBrowserRouter([{
  path: '/',
  element: <Navigation />,
  children: [
    {
      path: '/',
      element: <SearchBoard />
    },
    {
    path: '/:s',
    element: <SearchBoard />
  }, {
    path: '/country/:country',
    element: <Country />,
    children: [{
      path: '/country/:country',
      element: <Institutions />
    }, {
      path: '/country/:country/courses',
      element: <CountryCourses />
    }]
  },
   {
    path: '/courses/course/:id',
    element: <Course />
  }, {
    path: '/country/:country/:id',
    element: <Univercity />
  }, {
    path: '/dashboard',
    element: <Explore />
  },
  {
    path: '/saved-course',
    element: <SavedCourse />
  },
  {
    path: '/compare',
    element: <Comapre />
  },
  {
    path: '/profile',
    element: <Profile />,
    children: [
      {
        path: '/profile',
        element: <Personal />
      },
      {
        path: '/profile/english-test',
        element: <EnglishTest />
      },
      {
        path: '/profile/admission-test',
        element: <AdmissionTest />
      },
      {
        path: '/profile/education',
        element: <Education />
      },
      {
        path: '/profile/documents',
        element: <Documents />
      },
      {
        path: '/profile/employement',
        element: <Employement />
      }
    ]
  }
]
},{
  path: '/login',
  element: <Login />
}, {
  path: '/otp',
  element: <Otp />
}])

function App() {

  console.log('app render')

  const dispatch = useDispatch();

  useEffect(() => {
    (async () => {
          try {
            const {data} = await axios.get('http://localhost:5000/api/users/refresh', {withCredentials:true});
            if(data.status){
                dispatch(setAuth({user: data.user, is_login: true}))
            }
          } catch (error) {
            console.log(error);
          }
    })()
   }, [])

   useEffect(() => {
    (async () => {
      const { data } = await getPersonalData();
      if(data.personalDetail){
        dispatch(setPerDetdail({personal_detail: data.personalDetail}))
      }
    })()
   }, [])

   useEffect(() => {
    (async () => {
      const { data } = await getStudyPref();
    
      if(data.studyPreference){
        dispatch(setStudyPref({study_preference: data.studyPreference}))
    }
    })()
   }, [])

   useEffect(() => {
    (async () => {
      const { data } = await getEducationData();
      
      if(data.educationData[0]){
        dispatch(setEducation({education_detail: data.educationData[0]}))
     }
    })()
   }, [])

   useEffect(() => {
    (async () => {
      const { data } = await getEnglishData();
      console.log(data)
      if(data[0]){
        dispatch(setEngTest({english_test: data[0]}))
        console.log(data)
     }
    })()
   }, [])

   useEffect(() => {
    (async () => {
      const { data } = await getStaTest();
      console.log(data)
      if(data.standerTest){
        dispatch(setStanderTest({stander_test: data.standerTest}))
        console.log(data)
     }
    })()
   }, [])


  return (
    <div className="App">
     
      <RouterProvider router={router} />
     
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </div>
  );
}

export default App;
