import ProfileCard from '../../components/ProfileCrad/ProfileCard';
import { useState, useEffect } from 'react';
import { saveEnglishData, getEnglishData } from '../../Http';

const EnglishTest = () => {

    const [englishDetail, setEnglishDetail] = useState([{
        test_name: '',
        test_date: '',
        speaking: '',
        listening: '',
        writing: '',
        reading: '',
        over_all: ''
    }])

    const addEnglishField = () => {
        setEnglishDetail([
            ...englishDetail,
            {
                test_name: '',
                test_date: '',
                speaking: '',
                listening: '',
                writing: '',
                reading: '',
                over_all: ''
            }

        ])
    }

    const handleChange = (index, e) => {
        const updatedOpt = [...englishDetail];
        updatedOpt[index][e.target.name] = e.target.value;
        setEnglishDetail(updatedOpt)
    }

    const submitEnglishtest = async (index) => {
        const {data} = await saveEnglishData(englishDetail[index])
        console.log(data)
    }

    useEffect(() => {
        const getData = async () => {
            const {data} = await getEnglishData();
            if(data.englishData){
                setEnglishDetail(data.englishData)
            }
        }
        getData();
    }, [])

    return (
        <div>
           
           {
            englishDetail.map((eng, index) => {
                return (
                    <ProfileCard key={index} title="English Test Profile" subtitle="English Language Proficiency Test" tagline="Please enter the information for the Graduated English Language Proficiency Test Result.">
                    <div className="w-full mt-6 flex items-center">
                        <div className="left w-[49%]">
                            <div className="pform-container">
                                <label className="text-xs font-bold">Test Name</label>
                                <select name='test_name' onChange={(e) => handleChange(index, e)} className="profile_input" placeholder="select A Test">
                                    <option value='IELTS'>IELTS</option>
                                    <option value='Other'>Other</option>
                                </select>
                            </div>
                        </div>
                        <div className="w-[49%] md:w-[24%] ml-3">
                        <label className="text-xs font-bold">Date of Test</label>
                            <input type='date' name='test_date' value={eng.test_date} onChange={(e) => handleChange(index, e)} className='profile_input' placeholder="Test Date" />
                        </div>
                     </div>
                        <div className="w-full flex flex-wrap justify-between my-4">
                                <div className="w-[49%] md:w-[24%]">
                                   <label className="text-xs font-bold">Speaking</label>
                                   <select name='speaking' onChange={(e) => handleChange(index, e)} className="profile_input" placeholder="select a Score">
                                      <option value={1}>1</option>
                                      <option value={2}>2</option>
                                      <option value={3}>3</option>
                                      <option value={4}>4</option>
                                      <option value={5}>5</option>
                                   </select>
                                </div>
                                <div className="w-[49%] md:w-[24%]">
                                  <label className="text-xs font-bold">Listening</label>
                                   <select className="profile_input" name='listening' onChange={(e) => handleChange(index, e)} placeholder="select a Score">
                                      <option value={1}>1</option>
                                      <option value={2}>2</option>
                                      <option value={3}>3</option>
                                      <option value={4}>4</option>
                                      <option value={5}>5</option>
                                   </select>
                                </div>
                                <div className="w-[49%] md:w-[24%]">
                                  <label className="text-xs font-bold">Writing</label>
                                   <select className="profile_input" name='writing' onChange={(e) => handleChange(index, e)} placeholder="select a Score">
                                      <option value={1}>1</option>
                                      <option value={2}>2</option>
                                      <option value={3}>3</option>
                                      <option value={4}>4</option>
                                      <option value={5}>5</option>
                                   </select>
                                </div>
                                <div className="w-[49%] md:w-[24%]">
                                  <label className="text-xs font-bold">Reading</label>
                                   <select className="profile_input" name='reading' onChange={(e) => handleChange(index, e)} placeholder="select a Score">
                                      <option value={1}>1</option>
                                      <option value={2}>2</option>
                                      <option value={3}>3</option>
                                      <option value={4}>4</option>
                                      <option value={5}>5</option>
                                   </select>
                                </div>
                            </div>
                            <div className="w-full">
                            <div className="w-[49%] md:w-[24%]">
                                  <label className="text-xs font-bold">Overall</label>
                                   <select className="profile_input" name='over_all' onChange={(e) => handleChange(index, e)} placeholder="select a Score">
                                      <option value={1}>1</option>
                                      <option value={2}>2</option>
                                      <option value={3}>3</option>
                                      <option value={4}>4</option>
                                      <option value={5}>5</option>
                                   </select>
                                </div>
                            </div>
                            <div className="mt-4">
                                <button
                                 onClick={() => submitEnglishtest(index)}
                                 className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                            </div>
                </ProfileCard>
                )
            })
           }

            <div className='w-full mt-8'>
              <button
               onClick={addEnglishField}
               className='w-full bg-[#E2E2E2] py-1 rounded-2xl border border-[#707070] opacity-[.3]'><span className='text-[20px]'>+</span> Add More</button>
            </div>
        </div>
    )
}

export default EnglishTest;