import { useNavigate } from "react-router-dom";

const UnivercityCard = ({ univercity }) => {

    const navigate = useNavigate();

    return (
        <div>
            <div className="w-full border border-[#707070] rounded-xl p-4 pt-12 mx-auto">
                <div className="w-full flex justify-center pt-6 text-center">
                    {/* <img src={univercity.univercity_logo} className="h-[40px]" /> */}
                </div>
                <div className="my-10 text-center">
                    <h1 className="text-xl font-semibold">{ univercity.university_name }</h1>
                    <p className="text-[#7C878E]">{ univercity.university_address }</p>
                </div>
                <div className="text-center">
                    <button 
                     onClick={() => navigate(`/country/${univercity.university_location}/${univercity._id}`)}
                     className="border border-primary text-sm text-primary font-semibold py-2 px-6 hover:text-[#ffffff] hover:bg-primary rounded-3xl">Explore University</button>
                </div>
            </div>
        </div>
    )
}

export default UnivercityCard;