import ProfileCard from '../../components/ProfileCrad/ProfileCard';
import { useState, useEffect } from 'react';
import { saveEmployementData, getEmployementData } from '../../Http';


const Employement = () => {

    const [employedData, setEmployedData] = useState({
        occupation: '',
        satart_date: '',
        end_date: '',
        company: '',
        type_of_employment: '',
        field_of_experience: ''

    })

    const handleChange = (e) => {
        setEmployedData({
            ...employedData,
            [e.target.name]: e.target.value
        })
    }

    const submitData = async () => {
        console.log(employedData);
        const {data} = await saveEmployementData(employedData);
        console.log(data)
    }

    useEffect(() => {
        const getData = async () => {
            const { data } = await getEmployementData();
            //console.log(data)
            if(data.employData){
                setEmployedData(data.employData)
            }
        }
        getData();
    }, [])

  return (
    <div>

    <ProfileCard title="Employment Profile" subtitle="Employed Experience" tagline="Please enter the information for the Employed Experience. If not employed before, leave it blank.">
         <div className="flex w-full flex-wrap justify-between mt-4">
            <div className="w-full md:w-[49%]">
                <label className="text-xs font-bold">Occupation</label>
                <input className='profile_input' placeholder="Ocuupation..." name='occupation' onChange={(e) => handleChange(e)} value={employedData.occupation} />
            </div>
            <div className="w-[49%] md:w-[24%]">
                <label className="text-xs font-bold">Satart Date</label>
                <input className='profile_input' type='date' placeholder="Select Date" name='satart_date' onChange={(e) => handleChange(e)} value={employedData.satart_date} />
            </div>
            <div className="w-[49%] md:w-[24%]">
                <label className="text-xs font-bold">End Date</label>
                <input className='profile_input' type='date' placeholder="Select Date" name='end_date' onChange={(e) => handleChange(e)} value={employedData.end_date} />
            </div>
         </div>
         <div className="w-full mt-4 flex justify-between">
            <div className="w-[49%]">
                <label className="text-xs font-bold">Company</label>
                <input className='profile_input' type='text' placeholder="Fill Company Name" name='company' onChange={(e) => handleChange(e)} value={employedData.company} />
            </div>
            <div className="w-[49%]">
                <label className="text-xs font-bold">Type of Employment</label>
                <select className='profile_input' placeholder="Select a Employment Type" name='type_of_employment' value={employedData.type_of_employment} onChange={(e) => handleChange(e)}>
                    <option>Hello</option>
                    <option>Hello</option>
                    <option>Hello</option>
                </select>
            </div>
         </div>
         <div className="w-full mt-4 flex justify-between">
            
            <div className="w-[49%]">
                <label className="text-xs font-bold">Field of Experience</label>
                <select className='profile_input' placeholder="Field of Experience" value={employedData.field_of_experience} name='field_of_experience' onChange={(e) => handleChange(e)}>
                    <option>Hello</option>
                    <option>Hello</option>
                    <option>Hello</option>
                    <option>Hello</option>
                </select>
            </div>
         </div>
         <div className="mt-4">
                    <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl" onClick={submitData}>Submit</button>
                </div>
    </ProfileCard>

</div>
  )
}

export default Employement;