import SetupCard from "../../components/WixardCard/SetupCard";
import { useSelector, useDispatch } from "react-redux";
import { setEngTest } from "../../store/authSlice";
import { useState, useEffect } from "react";
import { toast } from 'react-toastify';
import { saveEnglishData } from "../../Http";

const Step4 = ({setStep}) => {

    const {english_test} = useSelector(state => state.auth);

    const [saving, setSaving] = useState(false);
    const dispatch = useDispatch()

    const [stapeData, setStapeData] = useState({
        exam_status: english_test.exam_status,
        test_name: english_test.test_name,
        speaking: english_test.speaking,
        listening: english_test.listening,
        writing: english_test.writing,
        reading: english_test.reading,
        over_all: english_test.over_all
    })


    const handleChange = (e) => {
          setStapeData({
            ...stapeData,
            [e.target.name]: e.target.value
          })
    }

    const handleNext = async () => {
        setSaving(true)
        const { data } = await saveEnglishData(stapeData);
        if(data.englishTest){
            console.log(data)
            dispatch(setEngTest({english_test: data.englishTest}));
            setStep(5)
        }
    }

    useEffect(() => {
        if(english_test.exam_status != null){
            setStep(5)
        }
    }, [english_test])

    useEffect(() => {
        setStapeData({
            exam_status: english_test.exam_status,
            test_name: english_test.test_name,
            speaking: english_test.speaking,
            listening: english_test.listening,
            writing: english_test.writing,
            reading: english_test.reading,
            over_all: english_test.over_all
        })
    }, [english_test])
 
    return (
        <SetupCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
       <div className="flex w-full justify-between mt-8">
           <div className="w-full">
               <label className="text-xs font-bold">English Exam Status</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="giv">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="exam_status" value='Already Gave the Exam' onChange={(e) => handleChange(e)} className="check_input hidden" id="giv" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Already Gave The Exam</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="yet">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="exam_status" value='Yet to give exam' className="check_input hidden" id="yet" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Yet To Give The Exam</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='exam_status' placeholder="select Gender" onChange={(e) => {handleChange(e)}}>
                    <option>Select</option>
                    <option value='Already Gave the Exam'>Already Gave the Exam</option>
                    <option value="Yet to give exam">Yet to give exam</option>
                </select> */}
           </div>
           
        </div>

     {
        stapeData.exam_status === 'Yet to give exam' ? (
            <div className="flex w-full justify-between mt-6">
            <div className="w-full relative flex items-center justify-start">
            <span class="text-xs font-bold justify-start mr-2"><input type="checkbox" name="score_type" class="custon_checkbox relative mr-2" value="Grade" /></span>
            Do you need help with English Exam Preparation?  
            </div>
             
            
         </div>
        ) : ''
     }
        {
            stapeData.exam_status === 'Already Gave the Exam' ? (
                <>
                <div className="flex w-full justify-between mt-6">
           <div className="w-full">
               <label className="text-xs font-bold">English Exam Completed</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="ie">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='IELTS' onChange={(e) => handleChange(e)} className="check_input hidden" id="ie" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">IELTS</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="to">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='TOEFL' className="check_input hidden" id="to" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">TOEFT</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="pt">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='PTE' className="check_input hidden" id="pt" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">PTE</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="du">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='Duolingo' className="check_input hidden" id="du" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Duolingo</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='test_name' placeholder="select Gender" onChange={(e) => handleChange(e)}>
                        <option value='IELTS'>IELTS</option>
                        <option value="TOEFL">TOEFL</option>
                        <option value='PTE'>PTE</option>
                        <option value='Duolingo'>Duolingo</option>
                </select> */}
           </div>
           
           
        </div>

        {
            stapeData.test_name === 'IELTS' ? (
                <div className="flex w-full justify-between mt-4">
           <div className="w-[19%]">
               <label className="text-xs font-bold">Overall</label>
               <input type='number' max={9} step={.1} className='wizard_input'  name='over_all' value={stapeData.over_all} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Speaking</label>
               <input type='number' step={.1} max={9} className='wizard_input' name='speaking' value={stapeData.speaking} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Listening</label>
               <input type='number' step={.1} max={9} className='wizard_input' name='listening' value={stapeData.listening} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Reading</label>
               <input type='number' step={.1} max={9} className='wizard_input' name='reading' value={stapeData.reading} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Writing</label>
               <input type='number' step={.1} max={9} className='wizard_input' name='writing' value={stapeData.writing} onChange={(e) => handleChange(e)} />
           </div>
           
        </div>
            ) : ''
        }

{
            stapeData.test_name === 'TOEFL' ? (
                <div className="flex w-full justify-between mt-4">
           <div className="w-[19%]">
               <label className="text-xs font-bold">Overall</label>
               <input type='number' max={120} step={1} className='wizard_input'  name='over_all' value={stapeData.over_all} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Speaking</label>
               <input type='number' step={1} max={30} className='wizard_input' name='speaking' value={stapeData.speaking} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Listening</label>
               <input type='number' step={1} max={30} className='wizard_input' name='listening' value={stapeData.listening} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Reading</label>
               <input type='number' step={1} max={30} className='wizard_input' name='reading' value={stapeData.reading} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Writing</label>
               <input type='number' step={1} max={30} className='wizard_input' name='writing' value={stapeData.writing} onChange={(e) => handleChange(e)} />
           </div>
           
        </div>
            ) : ''
        }

{
            stapeData.test_name === 'PTE' ? (
                <div className="flex w-full justify-between mt-4">
           <div className="w-[19%]">
               <label className="text-xs font-bold">Overall</label>
               <input type='number' min={10} max={90} step={1} className='wizard_input'  name='over_all' value={stapeData.over_all} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Speaking</label>
               <input type='number' step={1} min={10} max={90} className='wizard_input' name='speaking' value={stapeData.speaking} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Listening</label>
               <input type='number' step={1} min={10} max={30} className='wizard_input' name='listening' value={stapeData.listening} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Reading</label>
               <input type='number' step={1} min={10} max={30} className='wizard_input' name='reading' value={stapeData.reading} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Writing</label>
               <input type='number' step={1} min={10} max={30} className='wizard_input' name='writing' value={stapeData.writing} onChange={(e) => handleChange(e)} />
           </div>
           
        </div>
            ) : ''
        }

{
            stapeData.test_name === 'Duolingo' ? (
                <div className="flex w-full justify-between mt-4">
           <div className="w-[19%]">
               <label className="text-xs font-bold">Overall</label>
               <input type='number' min={10} max={160} step={1} className='wizard_input'  name='over_all' value={stapeData.over_all} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Conversation</label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='speaking' value={stapeData.speaking} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Literacy</label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='listening' value={stapeData.listening} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Comprehension</label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='reading' value={stapeData.reading} onChange={(e) => handleChange(e)} />
           </div>
           <div className="w-[19%]">
               <label className="text-xs font-bold">Production</label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='writing' value={stapeData.writing} onChange={(e) => handleChange(e)} />
           </div>
           
        </div>
            ) : ''
        }
                </>
            ) : ''
        }



        <div className="mt-4">
           <button
            onClick={handleNext}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>

                <button
           onClick={() => setStep(5)}
            className="text-sm ml-4 font-bold text-primary border border-primary py-2 px-6 rounded-3xl inline-flex items-center">
                Skip</button>
        </div>
       
   </SetupCard>
    )
}

export default Step4;