import { useNavigate } from "react-router-dom";

const FeaturedCourse = ({course}) => {


    const navigate = useNavigate()

    return (
        <div className="card">
        <div className="inner relative border border-[#AEAEAE] rounded-lg hover:border-[2px] border-[#7C878E] p-4">
            <p className="text-sm"><span>{course.course_level.length > 15 ? course.course_level.substring(0,15) + '..' : course.course_level}</span> <span className="block md:inline">{course.course_mode ? ': ' + course.course_mode : ''}</span></p>
            <div className="card-title h-[50px] my-6">
                <span className="text-xs opacity-[.5]">Course Code : {course.cricos_provider_code}</span>
                <h1 className="text-xl font-semibold">{course.course_name}</h1>
            </div>
            <div className="university flex w-full mb-4">
                <p className="text-md font-semibold text-primary">{course.aa_institute.university_name}</p>
                <span className="flex mx-6 font-semibold"><img className="mx-2" src='/assets/icons/place.svg' /> {course.aa_institute.university_address.length > 20 ? course.aa_institute.university_address.substring(0,20) + '..' : course.aa_institute.university_address}</span>
            </div>
            <div className="fees flex w-full mb-4">
                <div className="left w-1/2">
                    <span className="text-xs opacity-[.5]">Tuition Fee</span>
                    <p className="text-sm">$: {course.tutation_fees}</p>
                </div>
                <div className="right">
                    <span className="text-xs opacity-[.5]">Application Fee</span>
                    <p className="text-sm">$: {course.application_fees}</p>
                </div>
            </div>
            <div className='btn'>
                <button className="text-sm font-semibold rounded-3xl hover:text-[#ffffff] mr-4 hover:bg-primary text-primary py-1 px-4 border-[2px] border-primary">Start Application</button>
                <button  
                 onClick={() => navigate(`/courses/course/${course._id}`)}
                 className="text-sm font-semibold rounded-3xl mr-4 hover:bg-[#FFC9CF] text-primary py-2 px-6">Program Details</button>
            </div>
            <div className="action flex absolute top-4 right-4">
                <span className="w-[30px] h-[30px] hover:bg-[#7C878E] cursor-pointer bg-[#AEAEAE] flex rounded-[50%] text-[#ffffff] justify-center items-center"><i class="fa-solid fa-bookmark"></i></span>
            </div>
        </div>
    </div>
    )
}

export default FeaturedCourse;