import SetupCard from "../../components/WixardCard/SetupCard";
import { useSelector, useDispatch } from 'react-redux';
import { setEducation } from "../../store/authSlice";
import { useState, useEffect } from "react";
import { toast } from 'react-toastify';
import { saveEducationData } from "../../Http";

const Step3 = ({setStep}) => {

    const { education_detail } = useSelector(state => state.auth);
    const dispatch = useDispatch();

    const [stepData, setStepData] = useState({
        level_of_education: education_detail.level_of_education,
        course_status: education_detail.course_status,
        attended_till: education_detail.attended_till,
        marks: education_detail.marks,
        certificate: education_detail.certificate
    });

    const [saving, setSaving] = useState(false)
    const [file, setFile] = useState('Upload certificate')

    const handleChange = (e) => {
        setStepData({
            ...stepData,
            [e.target.name]: e.target.value
        })
    }

    const handleNext = async () => {

        if(!stepData.level_of_education){
            return (
                toast.error('Title is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }

        if(!stepData.course_status){
            return (
                toast.error('Course Status is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        if(stepData.course_status === 'Completed'){
            if(stepData.attended_till < 1000){
                return (
                    toast.error('Invalid Year', {
                        position: "bottom-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "light",
                      })
                )
            }
        }
        
        setSaving(true)
        const {data} = await saveEducationData(stepData);
        if(data.educationDetail){
           dispatch(setEducation({education_detail: data.educationDetail}))
           setStep(4)
        }
    }

    const handleCheckbox = (e) => {
        const arr = [];
        arr = arr.push(e.target.value);
        console.log(arr);
    }

    const handleCertificate = (e) => {

        const uploaderFile = e.target.files[0];
        setFile(uploaderFile.name);
        const reader = new FileReader();
        reader.readAsDataURL(uploaderFile);
        reader.onload = function(){
            setStepData({
                ...stepData,
                certificate: reader.result
            })
        }
        
    }

    useEffect(() => {
        console.log(stepData)
    }, [stepData])

    useEffect(() => {
        if(education_detail.level_of_education && education_detail.course_status){
            setStep(4)
        }
    }, [education_detail])



    useEffect(() => {
        setStepData({
            level_of_education: education_detail.level_of_education,
            course_status: education_detail.course_status,
            attended_till: education_detail.attended_till,
            marks: education_detail.marks,
            certificate: education_detail.certificate
        })
    }, [education_detail])

    return (
        <SetupCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
        <div className="flex w-full justify-between mt-8">
           <div className="w-full">
               <label className="text-xs font-bold">Highest Education</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="st">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" value='10h' onChange={(e) => handleChange(e)} className="check_input hidden" id="st" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">10th</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="12th">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" value='12th' className="check_input hidden" id="12th" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">12th</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="bach">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" value='Bachelors Degree' onChange={(e) => handleChange(e)} className="check_input hidden" id="bach" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Bachelors Degree</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="mas">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" value='Masters Degree' onChange={(e) => handleChange(e)} className="check_input hidden" id="mas" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Masters Degree</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="mb">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" value='MBBS / MD' onChange={(e) => handleChange(e)} className="check_input hidden" id="mb" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">MBBS / MD</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='level_of_education' placeholder="select Gender" onChange={(e) => handleChange(e)}>
                        <option value='10th'>10th</option>
                        <option value="12th">12th</option>
                        <option value="Bachelors Degree">Bachelors Degree</option>
                        <option value="Masters Degree">Masters Degree</option>
                        <option value='MBBS/MD'>MBBS/MD</option>
                </select> */}
           </div>
           
        </div>
        <div className="flex w-full justify-between mt-6">
           <div className="w-full">
               <label className="text-xs font-bold">Course Status</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="comp">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="course_status" value='Completed' onChange={(e) => handleChange(e)} className="check_input hidden" id="comp" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Completed</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="std">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="course_status" value='Studying' className="check_input hidden" id="std" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Studying</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='course_status' placeholder="select Gender" onChange={(e) => handleChange(e)}>
                        <option>Select</option>
                        <option value='Completed'>Completed</option>
                        <option value="Studying">Studying</option>
                </select> */}
           </div>
           
        </div>

        <div className="flex w-full justify-between mt-6">
           <div className="w-[32%]">
               <label className="text-xs font-bold">Completed Year</label>
               <input disabled={stepData.course_status === 'Completed' ? false : true } onChange={(e) => handleChange(e)} type='number' value={stepData.attended_till} className='wizard_input' placeholder="select a Test" name='attended_till' />
           </div>

           <div className="w-[32%]">
               <label className="text-xs font-bold">Marks (%)</label>
               <input disabled={stepData.course_status === 'Completed' ? false : true } onChange={(e) => handleChange(e)} value={stepData.marks} type='number' className='wizard_input' placeholder="select a Test" name='marks' />
           </div>

           <div className="w-[32%]">
               <label htmlFor="cert" className="text-xs font-bold">Certificate
                <div className="border-2 border-primary h-[45px] flex items-center pl-2 rounded-3xl text-primary">
                    {file}
                </div>
               </label>
               <input type='file' id='cert' disabled={stepData.course_status === 'Completed' ? false : true } className='hidden' placeholder="select a Test" name='certificate' onChange={(e) => handleCertificate(e)} />
           </div>
           
        </div>
       
       
       
       

        <div className="mt-4">
           <button
           onClick={handleNext}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
       
   </SetupCard>
    )
}

export default Step3;