import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    user: {
        email: ''
    },
    personal_detail: {
        first_name: null,
        last_name: null
    },
    study_preference: {
        education_country: [],
        field_of_study: null,
        level_of_study: null,
        start_year: null
    },
    education_detail: {
        level_of_education: null,
        course_status: null,
        attended_till: null,
        marks: null,
        certificate: null
    },
    english_test: {
        exam_status: null,
        test_name: null,
        speaking: null,
        listening: null,
        writing: null,
        reading: null,
        over_all: null
    },
    stander_test: {
        test_name: null,
        certificate: null
    },
    mobile: '',
    is_login: false

}

export const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        setAuth: (state, action) => {
            state.user = action.payload.user;
            state.is_login = action.payload.is_login
            
        },

        setPerDetdail: (state, action) => {
            state.personal_detail = action.payload.personal_detail
        },

        setStudyPref: (state, action) => {
            state.study_preference = action.payload.study_preference
        },
        setEducation: (state, action) => {
            state.education_detail = action.payload.education_detail
        },

        setEngTest: (state, action) => {
            state.english_test= action.payload.english_test
        },

        setStanderTest : (state, action) => {
            state.stander_test = action.payload.stander_test
        },
       
        setMobile: (state, action) => {

            //const {phone, hash} = action.payload;
            state.mobile = action.payload.mobile;
            //state.otp.hash = hash;
        }
       
    }
})

export const { setAuth, setMobile, setPerDetdail, setStudyPref, setEducation, setEngTest, setStanderTest } = authSlice.actions;
export default authSlice.reducer;

