import ProfileCard from '../../components/ProfileCrad/ProfileCard';
import { useState, useEffect } from 'react';
import { saveAddmissionData } from '../../Http';


const AdmissionTest = () => {

    const [testData, setTestData] = useState({
        test_name: ''
    })

    const handleChange = (e) => {
           
        setTestData({
            ...testData,
            [e.target.name] : e.target.value
        })
    }

    const submitData = async () => {
        const { data } = await saveAddmissionData(testData)
        console.log(data);
    }

    return (
        <div>
            <ProfileCard title="Admission Tests Profile" subtitle='General Test' tagline="Please enter the information for the Graduated General Test Result. If not taken any test, leave it blank.">
                <div className="w-full mt-6">
                <div className="left w-full md:w-1/2">
                        <div className="pform-container">
                            <label className="text-xs font-bold">Test Name</label>
                            <select className="profile_input" name='test_name' onChange={(e) => handleChange(e)} placeholder="Select A Test">
                                <option value='test 1'>Test 1</option>
                                <option value='test 2'>Test 2</option>
                                <option value='test 3'>Test 3</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div className="mt-4">
                                <button
                                 onClick={submitData}
                                 className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                            </div>
            </ProfileCard>
        </div>
    )
}

export default AdmissionTest;