import FeaturedCourse from "../../components/FeaturedCourse/FeaturedCourse";
import { useState, useEffect } from "react";
import { filterCourse } from "../../Http";
import { useParams } from "react-router-dom";
import Loader from '../../components/Loader/Loader';
import {Pagination} from 'antd';

const CountryCourses = () => {

    const {country} = useParams()
    console.log(country)

    const [courses, setCourses] = useState([]);
    const [loading, setLoading] = useState(true);
    const [page, setPage] = useState(0);
    const [totalRec, setTotalRec] = useState(0);
    const [totalPage, setTotalPage] = useState(0);

    useEffect(() => {
        setLoading(true)
        const getCourses = async () => {
            const {data} = await filterCourse(`?destination=${country}&page=${page}`)
            setCourses(data.requireddata);
            setTotalRec(data.totalFRecords);
            setTotalPage(data.totalFPage);
            setLoading(false)
            console.log(data)
        }
        getCourses();
    }, [country, page])

    return (
        <div className="w-full">
             <div className="search bg-[#FFF7F5] w-full py-3 flex justify-center">
                <div className="w-[80%] bg-[#ffffff] rounded-3xl px-3 py-1 border border-[#707070]">
                    <div className="w-full flex justify-between items-center">
                        <i class="fa-solid fa-magnifying-glass text-sm text-[#AEAEAE]"></i>
                        <input type="text" placeholder="Search Courses" className="w-[95%] font-semibold outline-none text-sm " />
                    </div>
                </div>
            </div>
            <div 
             className="top-filter flex justify-between mt-8">
              <div 
               className="left flex items-center">
                <div className="flex items-end">
                    <h1 className="text-2xl font-semibold mr-6 ">Featured Course In Australia</h1>
                        <span className="text-xs text-[#AEAEAE]">{totalRec}</span>
                </div>
                
                
               </div>
               </div>

               <div className="card-container my-4 grid grid-cols-1 md:grid-cols-2 gap-3 relative min-h-[300px]">

                {loading && <Loader />}

                {
                    courses.map((course) => <FeaturedCourse course={course} />)
                }
{/*               
                {
                //     courses.map((course) => <FeaturedCourse course={course} />)
                // } */}
            
            </div>
            <div className="mt-8 flex justify-center">
                <Pagination total={totalRec} pageSize={6} onChange={(page, pageSize) => setPage(page)} />
            </div>
        </div>
    )
}

export default CountryCourses;