import { useNavigate } from "react-router-dom";
import { SavedCourse, removeSavedCourse } from "../../Http/index";
import { useSelector } from "react-redux";
import { toast } from 'react-toastify';
import RenderCard from "./RenderCard";
import { useState, useEffect } from "react";
import { useDispatch } from 'react-redux';
// import { useSelector } from "react-redux";
import { setArray } from "../../store/authSlice";

function CourseCard({ data, setCompareBtn }) {


    const navigate = useNavigate();
    const [active, setActive] = useState(false);
    const [comparedCourse, setComparedCourse] = useState([])
   
  

    const dispatch = useDispatch();

    const {is_login} = useSelector((state) => state.auth);



    const handleClick = () => {
        setActive(!active);
       
    };

    useEffect(() => {
        let comStr = localStorage.getItem('compares');
        let compArr = JSON.parse(comStr);
        setComparedCourse(compArr);
    }, [])


    const addToCompare = (id) => {

        if(!is_login) {
            toast.error('You must be login', {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });
            return
        }

       

        let comStr = localStorage.getItem('compares');
        let compArr = JSON.parse(comStr);

        let index = compArr.findIndex((a) => a === id);

        if(index === -1){
        compArr.push(id);
        localStorage.setItem('compares', JSON.stringify(compArr));
        toast.success("Added to compare", {
            position: "bottom-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        });
        setComparedCourse(compArr);
    }else{

        compArr.splice(index, 1);
        localStorage.setItem('compares', JSON.stringify(compArr));
        toast.success("Removed from compare", {
            position: "bottom-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        })
        setComparedCourse(compArr)

    }

        if(compArr.length > 1){
            setCompareBtn('active-compare')
        }else{
            setCompareBtn('')
        }

    }

    useEffect(() => {
        let comStr = localStorage.getItem('compares');
        let compArr = JSON.parse(comStr);
        if(compArr.length > 1){
            setCompareBtn('active-compare')
        }else{
            setCompareBtn('')
        }
    }, [])


    const save_course = async () => {
        

        if(!is_login) {
            toast.error('You must be login', {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });
            return
        }
       

            const obj = {
                
                "course_id": data._id
            }

            var info = await SavedCourse(obj)

            if (info.data) {
                toast.success(info.data.message, {
                    position: "bottom-right",
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });


            } else {
                toast.error(info.data.message, {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });

            }
    }






    return (
        <div className="card">
            <div className="inner relative border border-[#AEAEAE] rounded-lg hover:border-[2px] border-[#7C878E] p-4" id={comparedCourse.includes(data._id) ? 'highlight-card' : ''}>
                <p className="text-sm"><span>{data.course_level.length > 15 ? data.course_level.substring(0, 15) + '..' : data.course_level}</span> <span className="block md:inline">{data.course_mode ? ': ' + data.course_mode : ''}</span></p>
                <div className="card-title h-[50px] my-6">
                    <span className="text-xs opacity-[.5]">Course Code : {data.cricos_provider_code}</span>
                    <h1 className="md:text-xl text-lg font-semibold">{data.course_name}</h1>
                </div>
                <div className="university flex w-full mb-4">
                    <p className="md:text-md text-sm font-semibold">{data.aa_institute.university_name}</p>
                    <span className="flex text-sm md:text-md mx-6 font-semibold"><img className="mx-2" src='/assets/icons/place.svg' /> {data.aa_institute.university_address.length > 20 ? data.aa_institute.university_address.substring(0, 20) + '..' : data.aa_institute.university_address}</span>
                </div>

                <div className="fees flex w-full mb-4">
                    <div className="left w-[30%]">
                        <span className="text-xs opacity-[.5]">Tuition Fee</span>
                        <p className="text-sm">$: {data.tutation_fees}</p>
                    </div>
                    <div className="right w-[30%]">
                        <span className="text-xs opacity-[.5]">Application Fee</span>
                        <p className="text-sm captilize">$ {data.application_fees}</p>
                    </div>
                    <div className="right">
                        <span className="text-xs opacity-[.5]">Course Duration</span>
                        <p className="text-sm captilize">$ {data.course_duration} Weeks</p>
                    </div>
                </div>
                <div className='btn'>
                    <button className="text-sm font-semibold rounded-3xl hover:text-[#ffffff] mr-4 hover:bg-primary text-primary py-1 px-4 border-[2px] border-primary">Start Application</button>
                    <button className="text-sm font-semibold rounded-3xl mr-4 hover:bg-[#FFC9CF] text-primary py-2 md:px-6" onClick={() => navigate(`/courses/course/${data._id}`)}>Program Details</button>
                </div>
                <div className="action flex absolute top-4 right-4">

                    {/* { add id="active-scolar for color bg } */}

                    <span
                        id={data.max_stipend || data.min_stipend ? 'active_scolar' : ''}
                        className='mr-4 bg-[#E2E2E2] flex items-center px-4 rounded-3xl text-sm text-[#ffffff]' >
                        Scholorship
                    </span>
                    <span onClick={() => addToCompare(data._id)} 
                    id={comparedCourse.includes(data._id) ? 'highlight-add-btn' : ''}
                    className="w-[30px] h-[30px] hover:bg-[#7C878E] cursor-pointer bg-[#AEAEAE] flex rounded-[50%] text-[#ffffff] justify-center items-center mr-1" style={{ backgroundColor: active ? "black" : "" }}><i class="fa-solid fa-code-compare"></i></span>
                    <span onClick={save_course} className="w-[30px] h-[30px] hover:bg-[#7C878E] cursor-pointer bg-[#AEAEAE] flex rounded-[50%] text-[#ffffff] justify-center items-center"><i className="fa-solid fa-bookmark"></i></span>
                </div>
            </div>
        </div>
    );
}

export default CourseCard;