import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import { useState, useEffect } from "react";

const setUpmap = {
    1: Step1,
    2: Step2,
    3: Step3,
    4: Step4,
    5: Step5,
}

const SetUp = ({setShowWizard, hrTabState}) => {

    const [step, setStep] = useState(1);

    const CurrentStep = setUpmap[step]

    return (
        <div className="w-full h-screen fixed top-0 left-0 upper bg-[rgba(0,0,0,.7)] flex justify-center items-center">
            <div className="w-[90%] max-w-[900px] relative">
                { hrTabState > 3 ? <button className="text-[#fff] text-lg absolute top-[-70px] right-[-70px]" onClick={() => setShowWizard(false)}>X</button> : '' }
             <CurrentStep setShowWizard={setShowWizard} setStep={setStep} />
            </div>
        </div>
    )
}

export default SetUp;