import SetupCard from "../../components/WixardCard/SetupCard";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setStanderTest } from "../../store/authSlice";
import { saveStaTest, getStaTest } from "../../Http";
import { toast } from 'react-toastify';

const Step5 = ({setShowWizard}) => {

    const {stander_test} = useSelector(state => state.auth);
    const [stepData, setStepData] = useState({
        test_name: stander_test.test_name,
        certificate: stander_test.certificate
    })
    const [saving, setSaving] = useState(false);
    const [file, setFile] = useState('Chhose a file')

    const dispatch = useDispatch();


    const handleChange = (e) => {
        setStepData({
            ...stepData,
            [e.target.name]: e.target.value
        })
    }

    const handleNext = async () => {

        if(!stepData.test_name){
            return (
                toast.error('Test name is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        setSaving(true);
        const {data} = await saveStaTest(stepData);
        if(data.standerTest){
            dispatch(setStanderTest({stander_test: data.standerTest}))
            setShowWizard(false);
        }
        console.log(data)
    }

    const handleFile = (e) => {
        const uploaderFile = e.target.files[0];
        setFile(uploaderFile.name);
        const reader = new FileReader();
        reader.readAsDataURL(uploaderFile);
        reader.onload = function(){
            setStepData({
                ...stepData,
                certificate: reader.result
            })
            console.log(stepData)
        }
    }


    useEffect(() => {
        setStepData({
            test_name: stander_test.test_name,
            certificate: stander_test.certificate
        })
    }, [stander_test])

    useEffect(() => console.log(stepData), [stepData])

    return (
        <SetupCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
        <div className="flex w-full justify-between mt-4">
           <div className="w-full">
               <label className="text-xs font-bold">Standardised Tests</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="gre">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='GRE' onChange={(e) => handleChange(e)} className="check_input hidden" id="gre" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">GRE</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="gmat">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='GMAT' className="check_input hidden" id="gmat" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">GMAT</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="sat">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" value='SAT' className="check_input hidden" id="sat" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">SAT</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='test_name' placeholder="select Gender" onChange={(e) => handleChange(e)}>
                        <option>Select</option>
                        <option value="GRE">GRE</option>
                        <option value="GMAT">GMAT</option>
                        <option value="SAT">SAT</option>
                </select> */}
           </div>
           
        </div>
        <div className="flex w-full justify-between mt-4">
           <div className="w-full">
               <label htmlFor="cert" className="text-xs font-bold">Mark Sheet/Certificate
               <div className="border-2 border-primary h-[45px] flex justify-center items-center pl-2 rounded-3xl text-primary text-center">
                    {file}
                </div>
               </label>
               <input type='file' id='cert' className="hidden" name="certificate" onChange={(e) => handleFile(e)} />
           </div>
           
           
        </div>



        <div className="mt-4">
           <button
           onClick={handleNext}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
                <button
           onClick={() => setShowWizard(false)}
            className="text-sm ml-4 font-bold text-primary border border-primary py-2 px-6 rounded-3xl inline-flex items-center">
                
                Skip</button>
        </div>
       
   </SetupCard>
    )
}

export default Step5;