import place from '../../assets/icons/place.svg';
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { removeSavedCourse } from "../../Http/index";
import { SavedCourse } from '../../Http/index';
import { toast } from 'react-toastify';


const Featuredcard = ({ course }) => {
    const navigate = useNavigate()

    const removeSaved = async (id) => {
        //const id = course.course_id._id
        var data = await SavedCourse({id})

        if (data.data.status) {
            toast.success("Course Remove Successfully...!", {
                position: "bottom-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });
            //navigate('/saved-course')


        } else {
            toast.error(data.data.message, {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });

        }

    }

    return (
        <div className="card">
            <div className="inner relative border border-[#AEAEAE] rounded-lg hover:border-[2px] border-[#7C878E] p-4">
                <p className="text-sm"> {course.course_id ? course.course_id.field_of_study : ""}</p>
                <div className="card-title h-[50px] my-6">
                    <h1 className="text-xl font-semibold">{course.course_name}</h1>
                </div>
                <div className="university flex w-full mb-4">
                    <p className="text-md font-semibold text-primary">{course.aa_institute.university_name}</p>
                    <span className="flex mx-6 font-semibold"><img className="mx-2" src={place} /> {course.aa_institute.university_location}</span>
                </div>
                <div className="fees flex w-full mb-4">
                    <div className="left w-1/2">
                        <span className="text-xs opacity-[.5]">Tuition</span>
                        <p className="text-sm"> $ {course.tutation_fees}</p>
                    </div>
                    <div className="right">
                        <span className="text-xs opacity-[.5]">Application Fee</span>
                        <p className="text-sm">$ {course.application_fees}</p>
                    </div>
                </div>
                <div className='btn'>
                    <button className="text-sm font-semibold rounded-3xl hover:text-[#ffffff] mr-4 hover:bg-primary text-primary py-1 px-4 border-[2px] border-primary">Start Application</button>
                    <button className="text-sm font-semibold rounded-3xl mr-4 hover:bg-[#FFC9CF] text-primary py-2 px-6" onClick={() => navigate(`/courses/course/${course._id}`)} >Program Details</button>
                </div>
                <div className="action flex absolute top-4 right-4" onClick={() => removeSaved(course._id)}>
                    <span className="w-[30px] h-[30px] hover:bg-[#7C878E] cursor-pointer bg-[#AEAEAE] flex rounded-[50%] text-[#ffffff] justify-center items-center red_color"><i className="fa-solid fa-bookmark "></i></span>
                </div>
            </div>
        </div>
    )

}

export default Featuredcard;