import { Outlet, useNavigate, Navigate } from "react-router-dom";
import FeaturedCourse from "./Featuredcard";
import React, { useState, useEffect } from 'react'
import { getSavedCourse } from "../../Http/index";
import Loader from '../../components/Loader/Loader';
import { useSelector } from "react-redux";
import { Pagination } from "antd";


const SavedCourse = () => {

  const [SavedData, setSavedData] = useState([])
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(0);
  const [totalPage, setTotalPage] = useState(0);
  const [total, setTotal] = useState(0);
  const navigate = useNavigate();
  const [sActive, setSactive] = useState(false);

  let {is_login} = useSelector((state) => state.auth)


  useEffect(() => {
    
      const Load_data = async () => {
        
  
        let data = await getSavedCourse(page);
        setSavedData(data.data.savedCourse)
        setTotal(data.data.total)
        setTotalPage(data.data.totalPage);
        setLoading(false)
        console.log(data)
        
      }
  
      Load_data()
  
    console.log(is_login)
   
  }, [page])



  

  if(!is_login){
    return <Navigate to='/login' />
  }

  if(is_login){
    return (
      <div>
        <div
          className="login-footer 
                     w-full
                     ">
          <div
            className="top 
                      justify-between
                      flex
                      w-[full] 
                      mx-auto 
                    ">
  
            <div
            id={sActive ? 'sactive' : ''}
              className="sidebar transition-all duration-500 pr-6 w-full md:w-1/2 left-[-100%] overflow-hidden fixed lg:static lg:w-[20%] z-10 bg-[#DBF9FB] py-8 px-4 h-[100vh]">
              <div
                className="sidebar-inner
                           rounded-xl">

{sActive ? (
                 <div className="right-4 mb-6">
                 <button className="lg:hidden" onClick={() => setSactive(false)}>
                     <i className="fa fa-times absolute left-[50%] translate-x-[-50%] font-xl"></i>
                 </button>
             </div>
             ) : ''}
  
  
                <div className=" flex flex-col">
                  <button className="border-2 hover:border-[#0A1C2B] py-2 w-90% rounded-3xl text-sm font-semibold text-[#AEAEAE] border-[#AEAEAE] hover:text-[#0A1C2B] my-2">Institutions</button>
                  <button className="border-2 border-[#0A1C2B] bg-[#0A1C2B] text-[#ffffff] py-2 w-90% rounded-3xl text-sm font-semibold my-2">Course</button>
                </div>
  
  
  
  
              </div>
  
            </div>
  
  
            <div className="h-[auto] w-full px-4 py-8 xl:pl-6">
              
             {!sActive ? (
                 <div className="fixed right-4">
                 <button className="lg:hidden" onClick={() => setSactive(true)}>
                     <i className="fa fa-bars font-xl"></i>
                 </button>
             </div>
             ) : ''}
  
              <div
                className="top-filter flex justify-between mt-8">
                <div
                  className="left flex items-center">
                  <div className="flex items-end">
                    <h1 className="text-2xl font-semibold mr-6 ">Saved Course</h1>
                    {/* <span className="text-xs text-[#AEAEAE]">98</span> */}
                  </div>
  
  
                </div>
                <div className="right flex">
                  <button className="mx-2 text-sm text-[#AEAEAE] font-semibold border border-[#AEAEAE] px-6 py-1 rounded-3xl"><i class="fa-solid fa-filter mr-3"></i> <span>Filter</span></button>
  
                  <div className="border relative border-[#AEAEAE] px-6 py-1 rounded-3xl text-sm text-[#AEAEAE]">
                    <i class="fa-solid fa-arrow-down-short-wide mr-4"></i>
                    <span className="mr-4">Sort</span>
                    <span className="transition-transform 
                                   duration-500
                                   peer-checked:rotate-90"><i class="fa-solid fa-chevron-down"></i></span>
  
                    <input
                      type="checkbox" checked
                      className="
                                   peer
                                   absolute top-0 inset-x-0 
                                   w-full h-12
                                   opacity-0 z-10 cursor-pointer
                                   "
                    />
                  </div>
                </div>
              </div>
  
  
              <div className="card-container my-4 grid grid-cols-1 md:grid-cols-2 gap-3">
  
              {loading && <Loader />}
  
              {
                  SavedData.map((d) => {
                    return <FeaturedCourse course={d.course} />
                 })
              }
  
               
             
  
              </div>
  
              {
                total > 6 ? <Pagination total={total} pageSize={6} onChange={(page, pageSize) => setPage(page)} /> : ''
              }
  
  
  
            </div>
  
  
  
  
  
  
          </div>
  
  
  
  
  
        </div>
  
  
  
      </div>
    )
  }
}

export default SavedCourse;