

const SetupCard = ({title, subtitle, tagline, children}) => {
    return (
        <div>
       
        <div className="bg-[#ffffff] p-8 rounded-2xl">
            <div>
                <h2 className="text-2xl font-semibold text-center">{ subtitle }</h2>
                <p className="text-sm text-center"><i>{ tagline }</i></p>
            </div>
            { children }
        </div>
    </div>
    )
}

export default SetupCard;