import UnivercityCard from "../../components/UnivercityCard/UnivercityCard";
import { useState, useEffect } from "react";
import { getAllUnivercityByCountry } from "../../Http";
import { useParams } from "react-router-dom";
import Loader from "../../components/Loader/Loader";
import {Pagination} from 'antd';

const Institutions = () => {

    const { country } = useParams();
    const [univercityData, setUnivercityData] = useState([])
    const [loading, setLoading] = useState(true);

    const [page, setPage] = useState(0);
    const [totalRec, setTotalRec] = useState(0);
    const [totalPage, setTotalPage] = useState(0);

    useEffect(() => {
        setLoading(true)
        const getUnivData = async () => {
           let { data } = await getAllUnivercityByCountry(page, country);
           setUnivercityData(data.requireddata);
           setTotalRec(data.totalFRecords);
           setTotalPage(data.totalFPage);
           setLoading(false);
          
        }
        getUnivData();
     },[country, page])


    return (
        <div className="w-full">
             <div className="search bg-[#FFF7F5] w-full py-3 flex justify-center">
                <div className="w-[80%] bg-[#ffffff] rounded-3xl px-3 py-1 border border-[#707070]">
                    <div className="w-full flex justify-between items-center">
                        <i class="fa-solid fa-magnifying-glass text-sm text-[#AEAEAE]"></i>
                        <input type="text" placeholder="Search Instution" className="w-[95%] font-semibold outline-none text-sm " />
                    </div>
                </div>
            </div>
            <div 
             className="top-filter flex justify-between mt-8">
              <div 
               className="left flex items-center">
                <div className="flex items-end">
                    <h1 className="text-2xl font-semibold mr-6 ">Featured Institutions In {country}</h1>
                        <span className="text-xs text-[#AEAEAE]">{totalRec}</span>
                </div>
                
                
               </div>
               </div>

               <div className="card-container my-4 grid lg:grid-cols-3 md:grid-cols-2 grid-cols-1 gap-3 relative min-h-[300px]">
                {loading && <Loader />}
                  

                  {
                    univercityData.map((univercity) => {
                        return <UnivercityCard key={univercity._id} univercity={univercity} />
                    })
                  }

                  
             
             </div>
             <div className="mt-8 flex justify-center">
                <Pagination total={totalRec} pageSize={6} onChange={(page, pageSize) => setPage(page)} />
             </div>
        </div>
    )
}

export default Institutions;