import { useState, useEffect } from "react";
import Loader from "../../components/Loader/Loader";
import { getComparedCourse } from "../../Http";
import { useNavigate } from "react-router-dom";
import Footer from "../../components/Footer/Footer";
import { toast } from 'react-toastify';
import { useSelector } from "react-redux";
import { SavedCourse } from "../../Http";


const Compare = () => {
    const [loading, setLoading] = useState(true);
    const [courses, setCourses] = useState([])

    const {is_login} = useSelector(state => state.auth)

    const navigate = useNavigate()


    const save_course = async (id) => {
        

      if(!is_login) {
          toast.error('You must be login', {
              position: "bottom-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              theme: "light",
          });
          return
      }
     

          const obj = {
              
              "course_id": id
          }

          var info = await SavedCourse(obj)

          if (info.data) {
              toast.success(info.data.message, {
                  position: "bottom-right",
                  autoClose: 3000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
                  theme: "light",
              });


          } else {
              toast.error(info.data.message, {
                  position: "bottom-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
                  theme: "light",
              });

          }
  }

    useEffect(() => {
        const fetchData = async () => {

            let comStr = localStorage.getItem('compares');
            let compArr = JSON.parse(comStr);
            let { data } = await getComparedCourse(compArr)
            setCourses(data)
            console.log(data)
            
        }
        fetchData()
    }, [])
    return (
        <div className="w-full">
        
            <div className="w-full flex overflow-scroll relative">
                <div className="w-1/4 min-w-[25%]">
                <div className="w-full bg-[#DBF9FB] border-r h-[250px] sticky right-0 left-0 shadow-md">
                      <div className='px-6 py-4 text-xs md:text-sm bg-[#ffffff]'>
                            <p><span className="cursor-pointer" onClick={() => navigate('/')}><i class="fa-solid fa-arrow-left"></i> Search Board</span> <i class="fa-solid fa-chevron-right"></i> 
                           <strong className="ml-2">Compare</strong></p>
                    </div>
                    <div>
                        <p className="text-center text-sm text-[#7C878E] font-[700] mt-4">Institution</p>
                        <p className="text-center text-sm text-[#7C878E] font-[700] mt-8">Course</p>
                    </div>
                  </div>
                   
                  <div className="">
                  <di className="w-full bg-[#DBF9FB] border-r h-[60px] flex items-center justify-center c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Course Duration</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Minimum Level of Education Completed</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Minimum GPA</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Estimated Total Cost</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Non-Tuition Fees</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Course Intakes</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r-2 h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Deadline</p>
                  </di>
                  <di className="w-full bg-[#DBF9FB] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Application Fee</p>
                  </di>
                  </div>
                </div>

                {
                    courses.map((cours) => (
                        <div className="w-1/4 min-w-[25%]" key={cours._id}>
                <div className="h-[250px] pt-6 top-[50px] w-full  shadow-md bg-[#ffffff]">
                            <img src={`${process.env.REACT_APP_API}${cours.aa_institute.univercity_logo}`} className="mx-auto h-[50px]" alt="logo" />
                            <div className="text-center mt-8 h-[50px]">
                                <p className="text-lg font-[600] mb-[-8px]">RMIT University</p>
                                <span className="text-sm text-[#7C878E] mt-[-10px]">{cours.aa_institute.university_location}</span>
                            </div>
                            <div className="text-center mb-6">
                                <h1 className="text-center mb-6">{cours.course_name.length > 25 ? cours.course_name.substring(0, 25) + '..' : cours.course_name}</h1>
                            </div>
                            <div className="flex justify-center items-center">
                                <span className="w-[30px] h-[30px] hover:bg-[#7C878E] cursor-pointer bg-[#AEAEAE] flex rounded-[50%] text-[#ffffff] justify-center items-center"
                                onClick={() => save_course(cours._id)}
                                ><i class="fa-solid fa-bookmark"></i></span>
                                <button className='bg-[#ffffff] text-primary py-1 border border-primary px-4 mx-6 rounded-3xl'>Start Application</button>
                                <a className="text-sm text-[#3045C5] border-b border-[#3045C5] cursor-pointer" onClick={() => navigate(`/courses/course/${cours._id}`)}>Details</a>
                            </div>
                        </div>
                  <di className="w-full bg-[#00000029] border-r h-[60px] flex items-center justify-center c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">{cours.course_duration} Weeks</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Minimum Level of Education Completed</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Minimum GPA</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">$ {cours.estimated_total_cost}</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">{cours.non_tut_fees}</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Course Intakes</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r-2 h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">Deadline</p>
                  </di>
                  <di className="w-full bg-[#00000029] border-r h-[50px] flex items-center justify-center  c-hover">
                    <p className="text-center text-sm text-[#7C878E] font-[700]">{cours.application_fees}</p>
                  </di>
                </div>
                    ))
                }
            </div>
            <Footer />
        </div>
    )
}


export default Compare;
