import ProfileCard from '../../components/ProfileCrad/ProfileCard'
import { select } from "antd";
import { DatePicker } from "antd";
import { Input } from "antd";
import { useState, useEffect } from 'react';
import { savePersonalData } from '../../Http';
import { getPersonalData } from '../../Http';

const Personal = () => {

    const [personalData, setPersonalData] = useState({
        first_name: '',
        last_name: '',
        dob: '',
        gender: '',
        country_of_birth: '',
        nationality: 'India',
        dual_citizenship: '',
        marital_status: ''
    })

    const handlePersonalData = (e) => {

        setPersonalData({
            ...personalData,
            [e.target.name]: e.target.value
        })

    }

    const submitPersonalData = async () => {
        console.log(personalData)
        const { data } = await savePersonalData(personalData);
        console.log(data)
        
    }

    useEffect(() => {
        const getPdata = async () => {
            const { data } = await getPersonalData();
            if(data.personalDetail) {
                setPersonalData(data.personalDetail)
                console.log(data.personalDetail)
            }
            //console.log(new Date(data.personalDetail.dob.toISOSting()));
        }
        getPdata()
    }, [])

    return (
        <div>

        <ProfileCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
             <div className="flex w-full justify-between mt-4 flex-wrap">
                <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">First Name</label>
                    <input className='profile_input' placeholder="select a Test" name='first_name' onChange={(e) => handlePersonalData(e)} value={personalData.first_name} />
                </div>
                <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">Last Name</label>
                    <input className='profile_input' placeholder="select a Test" onChange={(e) => handlePersonalData(e)} name='last_name' value={personalData.last_name} />
                </div>
                
             </div>
             <div className="w-full md:w-[49%] mt-4 flex justify-between">
                <div className="w-[49%]">
                    <label className="text-xs font-bold">Date of Birth</label>
                    
                    <input className='profile_input' type='date' value={personalData.dob} placeholder="select Date of Birth" onChange={(e) => handlePersonalData(e)} name='dob' value={personalData.dob} />
                </div>
                <div className="w-[49%]">
                    <label className="text-xs font-bold">Gender</label>
                    <select className='profile_input' name='gender' onChange={(e) => handlePersonalData(e)} placeholder="select Gender">
                        <option selected={personalData.gender === 'Male' ? true : false} value='Male'>Male</option>
                        <option selected={personalData.gender === 'Female' ? true : false} value="Female">Female</option>
                    </select>
                </div>
             </div>
             <div className="w-full mt-4 flex flex-wrap justify-between">
                
                <div className="w-[49%]">
                    <label className="text-xs font-bold">Country of Birth</label>
                    <select className='profile_input' name='country_of_birth' onChange={(e) => handlePersonalData(e)}  placeholder="select Country">
                        <option selected={personalData.country_of_birth === 'India' ? true : false} value='India'>India</option>
                        <option selected={personalData.country_of_birth === 'Nepal' ? true : false} value='Nepal'>Nepal</option>
                        <option selected={personalData.country_of_birth === 'Bangladesh' ? true : false} value='Bangladesh'>Bangladesh</option>
                    </select>
                </div>
                <div className="w-[49%] md:w-[24%]">
                    <label className="text-xs font-bold">Nationality</label>
                    <select className='profile_input' name='nationality' onChange={(e) => handlePersonalData(e)}  placeholder="select Nationality">
                        <option selected={personalData.nationality === 'Indial' ? true : false} value='Indian'>Indian</option>
                    </select>
                </div>
                <div className="w-full md:w-[24%]">
                    <label className="text-xs font-bold">Dual Citizenship / Nationality</label>
                    <select className='profile_input' name='dual_citizenship' onChange={(e) => handlePersonalData(e)} placeholder="select Nationality">
                        <option selected={personalData.dual_citizenship === 'Usa' ? true : false} value='Usa'>Usa</option>
                        <option selected={personalData.dual_citizenship === 'Australia' ? true : false} value='Australia'>Australia</option>
                        <option selected={personalData.dual_citizenship === 'Canada' ? true : false} value='Canada'>Canada</option>
                    </select>
                </div>
             </div>
             <div className="w-full mt-4 flex justify-between">
                
                
                <div className="w-full md:w-[24%]">
                    <label className="text-xs font-bold">Marital Status</label>
                    <select className='profile_input' name='marital_status' onChange={(e) => handlePersonalData(e)} placeholder="select Marital Status">
                        <option selected={personalData.marital_status === 'Married' ? true : false} value='Married'>Married</option>
                        <option selected={personalData.marital_status === 'Unmarried' ? true : false} value='Unmarried'>Unmarried</option>
                    </select>
                </div>
             </div>

             <div className="mt-4">
                <button
                onClick={submitPersonalData}
                 className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
             </div>
            
        </ProfileCard>

        <ProfileCard subtitle="Contact Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
             <div className="flex w-full flex-wrap justify-between mt-4">
                <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">Addree</label><br/>
                    <textarea placeholder="select a Test" className='profile_input' id='textarea' value="John" ></textarea>
                </div>
                <div className="w-full md:w-[49%] flex justify-between flex-wrap">
                 

                    <div className="w-[49%]">
                    <label className="text-xs font-bold">State</label>
                    <select className="profile_input" placeholder="select a State">
                        <option>Hello</option>
                    </select>
                    </div>

                    <div className="w-[49%]">
                    <label className="text-xs font-bold">City</label>
                    <select className="profile_input" placeholder="select City">
                        <option>Hello</option>
                    </select>

                    </div>
                    <div className="w-[49%]">
                    <label className="text-xs font-bold">Postal Code</label>
                    <input type='text' className='profile_input' placeholder="select a Test" value="50000" />
                    </div>

                </div>
                
             </div>
             

             <div className="mt-4">
                            <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                        </div>
             
            
        </ProfileCard>

        <ProfileCard subtitle="Study Preference Details" tagline="Please enter the details as per your study search preference.">
             <div className="flex w-full flex-wrap justify-between mt-4">

                <div className="w-full flex justify-between flex-wrap">
                 

                    <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">Education Country</label>
                    <select className="profile_input" placeholder="India">
                        <option>Hello</option>
                    </select>
                    </div>

                    <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">Preferred Study Destination</label>
                    <select className="profile_input" placeholder="Australia">
                        <option>Hello</option>
                    </select>

                    </div>
                    

                    <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">Preferred Area of Study</label>
                    <select className="profile_input" placeholder="Agriculture">
                        <option>Hello</option>
                    </select>

                    </div>
                    

                    <div className="w-full md:w-[49%]">
                    <label className="text-xs font-bold">Agriculture</label>
                    <select className="profile_input" placeholder="Bachelor">
                        <option>Hello</option>
                    </select>

                    </div>
                    

                </div>
                
             </div>
             <div className="mt-4">
                            <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                        </div>
        </ProfileCard>

    

    </div>
    )
}

export default Personal;