import ProfileCard from '../../components/ProfileCrad/ProfileCard'
import { mailOtp, verifyMail } from '../../Http';
import { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import SetupCard from '../../components/WixardCard/SetupCard';
import { setAuth, setPerDetdail } from '../../store/authSlice';
import { useDispatch, useSelector } from 'react-redux';
import { savePersonalData } from '../../Http';


const Step1 = ({setStep}) => {

    const [optSent, setOtpSent] = useState(false)
    const [email, setEmail] = useState()
    const [otp, setOtp] = useState();
    const [sending, setSending] = useState(false)
    const [veryfying, setVerifying] = useState(false)
    const [saving, setSaving] = useState(false)
    

    const dispatch = useDispatch();

    const {user} = useSelector(state => state.auth)
    const {personal_detail} = useSelector(state => state.auth)

    // const [verified, setVerified] = useState(user.email.length > 1 ? true : false)

    const [stepData, setStepData] = useState({first_name: personal_detail.first_name, last_name: personal_detail.last_name});

    const sendOtp = async () => {

        if(!email){
            return (
                toast.error('Email is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }

        setSending(true)

        const {data} = await mailOtp({email});
        if(data.status){
            sessionStorage.setItem('emailhash', data.hash);
            sessionStorage.setItem('email', data.email);
            setOtpSent(true)
        }
        console.log(data)
    }

    const verifyOtp = async () => {
        if(!otp){
            return (
                toast.error('Otp is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        if(otp.length < 6){
            return (
                toast.error('Invalid OTP', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }

        setVerifying(true)
        const otpData = {otp, hash: sessionStorage.getItem('emailhash'), email: sessionStorage.getItem('email')}
        console.log(otpData)
        const { data } = await verifyMail(otpData)
        console.log(data)
        if(data.status == false){
            return (
                toast.error(data.message, {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        if(data.status){
            dispatch(setAuth({user: data.updUser}));
            //setVerified(true)
        }
    }

    const handleNExt = async () => {
        // if(user.email === ''){
        //     return (
        //         toast.error('Email not verified', {
        //             position: "bottom-right",
        //             autoClose: 5000,
        //             hideProgressBar: false,
        //             closeOnClick: true,
        //             pauseOnHover: true,
        //             draggable: true,
        //             progress: undefined,
        //             theme: "light",
        //           })
        //     )
        // }

        setSaving(true)
        //setStep(2)

        const {data} = await savePersonalData(stepData);
        console.log(data)
        if(data.first_name != null){
            dispatch(setPerDetdail({personal_detail: data}));
            console.log(data)
            setStep(2)
        }
    }

    const handleChange = (e) => {

        setStepData({
            ...stepData,
            [e.target.name]: e.target.value
        })
        
    }

    useEffect(() => {
        setStepData({first_name: personal_detail.first_name, last_name: personal_detail.last_name})
    }, [personal_detail])

    useEffect(() => {
        if(personal_detail.first_name != null && personal_detail.last_name != null){
            setStep(2)
        }
    }, [personal_detail])

   


    return (
        
        <SetupCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
        <div className="flex w-full justify-between mt-4">
        
           <div className="w-full">
               <label className="text-xs font-bold">First Name</label>
               <input className='wizard_input' placeholder="select a Test" name='first_name' value={stepData.first_name} onChange={(e) => handleChange(e)} />
           </div>
           
        </div>
        <div className="flex w-full justify-between mt-4">
           <div className="w-full">
               <label className="text-xs font-bold">Last Name</label>
               <input className='wizard_input' placeholder="select a Test" name='last_name' value={stepData.last_name} onChange={(e) => handleChange(e)} />
           </div>
           
        </div>
        <div className="flex w-full justify-between mt-4">
          {!optSent && user.email === '' ? (<div className="w-full">
               <label className="text-xs font-bold">Email</label>
               <div className='flex justify-between'>
               <input type='email' value={email} onChange={(e) => setEmail(e.target.value)} className='wizard_input' placeholder="select a Test" name='last_name' />
               <button
               onClick={sendOtp}
               className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl ml-2 inline-flex items-center">
                {sending ? <span className='load'></span> : ''}
                Otp</button>
               </div>
           </div>) : ''}


          {optSent && user.email === '' ? (<div className="w-full">
               <label className="text-xs font-bold">Otp</label>
               <div className='flex justify-between'>
               <input type='email' value={otp} onChange={(e) => setOtp(e.target.value)} className='wizard_input' placeholder="select a Test" name='last_name' />
               <button
               onClick={verifyOtp}
               className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl ml-2 inline-flex items-center">
                {veryfying ? <span className='load'></span> : ''}
                Verify</button>
               </div>
           </div>) : ''}

           {user.email.length > 2 ? (<div className="w-full">
               <label className="text-xs font-bold">Otp</label>
               <div className='flex justify-between'>
               <input disabled={true} type='email' value={user.email} className='wizard_input' placeholder="select a Test" name='last_name' />
               <button
                disabled={true}
               className="text-sm font-bold text-[#ffffff] bg-green-700 py-2 px-6 rounded-3xl ml-2 inline-flex items-center">
                Verified</button>
               </div>
           </div>) : ''}
           
        </div>
       
       
       

        <div className="mt-4">
           <button
           onClick={handleNExt}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">

{saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
       
   </SetupCard>

    )
}

export default Step1;