import ProfileCard from '../../components/ProfileCrad/ProfileCard'
import { select } from "antd";
import { DatePicker } from "antd";
import { Input } from "antd";
import { useState, useEffect } from 'react';
import { saveEducationData, getEducationData } from '../../Http';

const Education = () => {

  const [educationDetail, setEducationDeatil] = useState([{
    level_of_education: '',
    degree_name: '',
    name_of_institution: '',
    attended_from: '',
    attended_till: '',
    primary_language_of_instruction: '',
    score_type: '',
    score: '',
    country_of_institution: ''
  }])

  const addEducationField = () => {
    setEducationDeatil([
      ...educationDetail,
      {
        level_of_education: '',
        degree_name: '',
        name_of_institution: '',
        attended_from: '',
        attended_till: '',
        primary_language_of_instruction: '',
        score_type: '',
        score: '',
        country_of_institution: ''
      }
    ])
  }

  const handleEducation = (index, e) => {
    const updatedOpt = [...educationDetail];
    updatedOpt[index][e.target.name] = e.target.value;
    setEducationDeatil(updatedOpt)
  }

  const submitEducation = async (index) => {
    const {data} = await saveEducationData(educationDetail[index])
    console.log(data)
  }

  useEffect(() => {
    const getData = async () => {
      const {data} = await getEducationData();
      if(data.educationData){
        setEducationDeatil(data.educationData)
      }
      //console.log(data)
    }
    getData();
  }, [])

    return (
        <div>
            
            {
              educationDetail.map((edu, index) => {
                return (
                  <ProfileCard key={index} title="English Test Profile" subtitle="English Language Proficiency Test" tagline="Please enter the information for the Graduated English Language Proficiency Test Result.">
                  <div className="w-full mt-6 flex justify-between items-center">
                      <div className="left w-full md:w-[49%]">
                          <div className="pform-container">
                              <label className="text-xs font-bold">Level of Education</label>
                              <select value={edu.level_of_education} name='level_of_education' onChange={(e) => handleEducation(index, e)} className="profile_input" placeholder="select the Level of Education">
                                  <option>Hello</option>
                                  <option>Hello</option>
                                  <option>Hello</option>
                              </select>
                          </div>
                      </div>
                   </div>
                      <div className="w-full flex justify-between my-4">
                              <div className="w-[49%]">
                                 <label className="text-xs font-bold">Degree Name</label>
                                 <input name='degree_name' value={edu.degree_name} type='text' placeholder="Fill Degree Name" className='profile_input' onChange={(e) => handleEducation(index, e)} />
                              </div>
                              <div className="w-[49%]">
                                <label className="text-xs font-bold">Name of Institution</label>
                                <input type='text' placeholder="Name of Institution" name='name_of_institution' value={edu.name_of_institution} className='profile_input' onChange={(e) => handleEducation(index, e)} />
                              </div>
                          </div>
                          <div className="w-full flex flex-wrap justify-between">
                          <div className="f-[49%] md:w-[24%]">
                                
                                <label className="text-xs font-bold">Attended from</label>
                                <input type='date' name='attended_from' value={edu.attended_from} onChange={(e) => handleEducation(index, e)} placeholder="select Date" className='profile_input' />
                              </div>
                              <div className="w-[49%] md:w-[24%]">
                                
                                <label className="text-xs font-bold">Attended till</label>
                                <input type='date' name='attended_till' value={edu.attended_till} onChange={(e) => handleEducation(index, e)} placeholder="select Date" className='profile_input' />
                              </div>
                              <div className="w-full md:w-[49%]">
                                
                                <label className="text-xs font-bold">Primary Language of Instruction</label>
                                <select name='primary_language_of_instruction' onChange={(e) => handleEducation(index, e)} placeholder="English" className='profile_input'>
                                  <option value='English'>English</option>
                                  <option value='English'>Hindi</option>
                                  <option value="Others">Others</option>
                              </select>
                              </div>
                          </div>
                          <div className="w-full flex flex-wrap justify-between my-4">
                              <div className="w-full md:w-[49%]">
                                  <div className="w-full flex mb-2">
                                    <label className="text-xs font-bold">Overall Score</label>
                                    <span className="text-xs font-bold flex mx-8"><input type='radio' value='Grade' name='score_type' onChange={(e) => handleEducation(index, e)} className="custon_checkbox relative mr-2" /> Grade</span>
                                    <span className="text-xs font-bold flex whitespace-nowrap"><input type='radio' value='Overall Score' name='score_type' onChange={(e) => handleEducation(index, e)} className="custon_checkbox relative mr-2" /> Overall Score</span>
                                  </div>
                                 <input className='profile_input' value={edu.score} type='number' placeholder="100%" name='score' onChange={(e) => handleEducation(index, e)} />
                              </div>
                              <div className="w-full md:w-[49%]">
                                <label className="text-xs font-bold">Country of Institution</label>
                                <select className="profile_input" placeholder="select a Country" name='country_of_institution' value={edu.country_of_institution} onChange={(e) => handleEducation(index, e)}>
                                  <option value='India'>India</option>
                                  <option value='Nepal'>Nepal</option>
                                  <option value='Shri Lanka'>Shri Lanka</option>
                              </select>
                              </div>
                          </div>
                          <div className="mt-4">
                              <button 
                               onClick={() => submitEducation(index)}
                               className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                          </div>
                  
              </ProfileCard>
                )
              })
            }

            <div className='w-full mt-8'>
              <select className='w-full bg-[#E2E2E2] py-1 rounded-2xl border border-[#707070] opacity-[.3]'>
                <option>Grade 10</option>
              </select>
            </div>
            <div className='w-full mt-8'>
              <button
               onClick={addEducationField}
               className='w-full bg-[#E2E2E2] py-1 rounded-2xl border border-[#707070] opacity-[.3]'><span className='text-[20px]'>+</span> Add More</button>
            </div>
        </div>
    )
}


export default Education;