import LoginCard from "../../components/logincard/LoginCard";
import React, { useState, useEffect } from 'react'
import { VerifyOtp, SendOtp } from "../../Http/index";
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { toast } from 'react-toastify';
import { useSelector } from "react-redux";
import { useDispatch } from 'react-redux';
import { setAuth } from "../../store/authSlice";
import { setLogin } from "../../store/authSlice";
import OTPInput from "otp-input-react";




const Otp = () => {
  const [First, setFirst] = useState(false)
  const [Second, setSecond] = useState(false)
  const [Third, setThird] = useState(false)
  const [Fourth, setFourth] = useState(false)
  const [Five, setFive] = useState(false)
  const [Six, setSix] = useState(false)
  const navigate = useNavigate()
  const dispatch = useDispatch();
  const [OTP, setOTP] = useState("");

  const { mobile } = useSelector((state) => state.auth)

  

  const Verify = async (e) => {
    e.preventDefault()
    

    if (!OTP || OTP.length < 6) {
    
      const otp = First + Second + Third + Fourth + Five + Six
      const obj = {
        "mobile": mobile,
        "otp": parseInt(OTP)
      }

      const data = await VerifyOtp(obj)
      
     


      console.log("data", data)
      if (data.data.status === true) {


        //sessionStorage.setItem("is_login", true);
        //sessionStorage.setItem("login_id", data.data.data._id);


        
        dispatch(setAuth({user: data.user, is_login: true}))
        navigate('/dashboard')

        // dispatch(setLoginId({ login_id: data.data.data._id }))

        // dispatch(setLogin({is_login: true, user: data.data.user}))


        

      } else {
        toast.error(data.data.message, {
          position: "bottom-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
        });

      }
    } else {

      toast.error("Please Enter OTP.....!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });

    }



  }

  const sendOtp1 = async (e) => {
    e.preventDefault()


    var data = await SendOtp({ "mobile": mobile })


    if (data.data.status === true) {
    
      toast.success('OTP Send Successfully ....!', {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
     

    } else {

      toast.error(data.data.message, {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    }


  }




  return (
    <div className="w-full h-[100vh] bg-[#dedede]">
      <div className="max-w-screen-lg mx-auto h-[100%] flex items-center justify-end">
        <LoginCard>
          <div className="w-[90%] mx-auto text-center">
            <label className="form-label w-[90%] mx-auto text-center">Enter OTP</label>
            <div className="my-4 w-[90%] mx-auto text-center mb-4">
              {/* <input
                type='text'
                autoFocus={true}
                maxLength={1}
                onChange={(e) => { setFirst(e.target.value) }}
                className='outline-none 
                               border-b-[2px] 
                               border-[#0A1C2B] 
                               w-[30px] 
                               pl-[14px] mr-2'
              /> */}
              {/* <input
                type='text'
                autoFocus={First ? true : false}
                maxLength={1}
                onChange={(e) => { setSecond(e.target.value) }}
                className='outline-none 
                               border-b-[2px] 
                               border-[#0A1C2B] 
                               w-[30px] 
                               pl-[14px] mr-2'
              /> */}
              {/* <input
                type='text'
                maxLength={1}
                onChange={(e) => { setThird(e.target.value) }}
                className='outline-none 
                               border-b-[2px] 
                               border-[#0A1C2B] 
                               w-[30px] 
                               pl-[14px]  mr-8'
              /> */}
              {/* <input
                type='text'
                maxLength={1}
                onChange={(e) => { setFourth(e.target.value) }}
                className='outline-none 
                               border-b-[2px] 
                               border-[#0A1C2B] 
                               w-[30px] 
                               pl-[14px] mr-2'
              /> */}
              {/* <input
                type='text'
                maxLength={1}
                onChange={(e) => { setFive(e.target.value) }}
                className='outline-none 
                               border-b-[2px] 
                               border-[#0A1C2B] 
                               w-[30px] 
                               pl-[14px] mr-2'
              /> */}
              {/* <input
                type='text'
                maxLength={1}
                onChange={(e) => { setSix(e.target.value) }}
                className='outline-none 
                               border-b-[2px] 
                               border-[#0A1C2B] 
                               w-[30px] 
                               pl-[14px]'
              /> */}
              <OTPInput value={OTP} inputStyles={{outline: 'none', borderBottom: '2px solid #000000'}} onChange={setOTP} autoFocus OTPLength={6} otpType="number" disabled={false} secure />
            </div>
            <div className="flex justify-between w-[90%] mx-auto my-8">
              <button className='px-10 py-2 border border-primary bg-[#ffffff] text-primary text-sm font-[500] rounded-3xl hover:bg-primary hover:text-[#ffffff] cursor-pointer' onClick={sendOtp1}>Resend</button>
              <button className='bg-primary text-[#ffffff] py-2 px-10 rounded-3xl' onClick={Verify}>Login</button>

            </div>
            <div>
              <a>Cancel</a>
            </div>
          </div>
        </LoginCard>
      </div>
    </div>
  )
}

export default Otp;