import { getAllUnivercityByCountry, getUnivercityById, filterCourse, getAllCourse } from "../../Http";
import { useEffect,useState } from "react";
import {useParams} from 'react-router-dom';
import FeaturedCourse from '../../components/FeaturedCourse/FeaturedCourse';
import Footer from "../../components/Footer/Footer";
import {Pagination} from 'antd';
import Loader from "../../components/Loader/Loader";
import { useNavigate } from "react-router-dom";


const Univercity = () => {

    const { id } = useParams();
    const [univercityData, setUnivercityData] = useState([{}]);
    const [couseOffered, setCourseOffered] = useState([]);

    const [page, setPage] = useState(0);
    const [totalRec, setTotalRec] = useState(0);
    const [totalPage, setTotalPage] = useState(0);

    const [loading, setLoading] = useState(true);

    const navigate = useNavigate();


    useEffect(() => {
        const getunivercity = async () => {
            const { data } = await getUnivercityById(id);
            setUnivercityData(data.requireddata)
            console.log(data)
        }
        getunivercity();
        

    }, [id])

    useEffect(() => {

        setLoading(true);
        const simlilar = async () => {
            
            const {data} = await filterCourse(`?aa_institute=${id}&page=${page}`);

            console.log(data)
           
            setCourseOffered(data.requireddata);
            setTotalRec(data.totalFRecords);
            setTotalPage(data.totalFPage);

            
        }
        simlilar()
        setLoading(false)
    }, [page])


    return (
        <div className="courseWrapper w-full">
        <div className='px-6 py-4 text-xs md:text-sm'>
            <p><span className="cursor-pointer" onClick={() => navigate('/')}><i class="fa-solid fa-arrow-left px-2"></i> Search Board</span> <i class="fa-solid fa-chevron-right px-2"></i> 
            <span className="cursor-pointer" onClick={() => navigate(`/country/${univercityData[0].university_location}`)}>{univercityData[0].university_location}</span> <i class="fa-solid fa-chevron-right px-2"></i> 
            <strong>{univercityData[0].university_name}</strong></p>
        </div>
        <div className="course w-full flex">
            <div className="sidebar md:w-[280px] w-[0] overflow-hidden">
                <div className="universicy bg-[#DBF9FB] py-8 px-4 rounded-md w-full">
                    <div className="intro text-center">
                        <img src={`http://139.59.25.83:5000${univercityData[0].univercity_logo}`} alt='logo' className='w-[150px] mx-auto mb-6' />
                        <div className='heading mb-6'>
                           <h1 className='md:text-xl text-lg font-bold text-secondary'>{ univercityData[0].university_name }</h1>
                           <span className='text-sm font-[500] text-[#7C878E] mt-[-10px]'>{ univercityData[0].university_address }</span>
                        </div>
                        <div className='map'>
                            <p className='text-[#7C878E] font-[500] text-md'><i class="fa-solid fa-map-location"></i> Melbourne, Victoria</p>
                        </div>
                    </div>
                    <div className='info mt-12'>
                        {univercityData[0].about_university && <h1 className='text-lg font-[500] text-secondary'>About</h1>}
                        <p className='text-[#586A78] text-md leading-[19px]'>{ univercityData[0].about_university }</p>
                    </div>
                    <div className='button mt-12 text-center'>
                        <button className='px-6 py-2 border border-primary bg-[#ffffff] text-primary text-sm font-[500] rounded-3xl hover:bg-primary hover:text-[#ffffff] cursor-pointer'>Explore University</button>
                    </div>
                    <div className='website text-center mt-12'>
                        <a className='text-sm font-bold text-[#0A1C2B]' href={ univercityData[0].university_website}>Website</a>
                    </div>
                </div>
            </div>
            <div className="main md:pl-6 w-full mx-2 md:pr-6">
                <div className='main-inner'>
                    <div className='heading flex flex-col md:flex-row justify-between w-full md:w-[90%]'>
                        <div className='left'>
                            <h1 className='md:text-2xl text-lg font-bold text-[#0A1C2B]'>{ univercityData[0].university_name }</h1>
                            <span className='text-xs text-[#7C878E]'>{ univercityData[0].university_address }</span>
                        </div>
                        <div className='right mt-6 md:mt-0 flex items-center'>
                            <div className='innerleft mr-4'>
                                <button className='bg-primary text-[#ffffff] py-2 px-6 rounded-3xl'>Download Prospectus</button>
                            </div>
                        </div>
                    </div>

                    <div className='gallery flex mt-4 md:mt-0 flex-col md:flex-row space-4 mt-2'>
                        <div className='item1 md:w-1/2 md:pr-2 w-full'>
                            <img className="rounded-lg" src={univercityData[0].banners ? univercityData[0].banners[0] : '/assets/images/und1.png'} alt='univercity01' />
                        </div>
                        <div className='flex flex-wrap md:w-1/2 mt-4 md:mt-0 md:pl-2 w-full'>
                        <div className='item1 w-1/2 pb-2 pr-2'>
                           <img className="rounded-lg" src={univercityData[0].banners ? univercityData[0].banners[0] : '/assets/images/und2.png'} alt='univercity01' />
                        </div>
                        <div className='item1 w-1/2 pb-2 pl-2'>
                          <img className="rounded-lg" src={univercityData[0].banners ? univercityData[0].banners[0] : '/assets/images/und3.png'} alt='univercity01' />
                        </div>
                        <div className='item1 w-1/2 pt-2 pr-2'>
                           <img className="rounded-lg" src={univercityData[0].banners ? univercityData[0].banners[0] : '/assets/images/und4.png'} alt='univercity01' />
                        </div>
                        <div className='item1 w-1/2 pt-2 pl-2'>
                           <img className="rounded-lg" src={univercityData[0].banners ? univercityData[0].banners[0] : '/assets/images/und5.png'} alt='univercity01' />
                        </div>
                        </div>
                    </div>

                    <div className="md:hidden pt-6">
                        {univercityData[0].about_university && <h1 className='text-md font-bold'>About Unovercity</h1>}
                        {univercityData[0].about_university && <p>{univercityData[0].about_university}</p>}

                        <div className="w-full flex justify-between mt-4">
                      
                        <button className='px-6 py-2 border border-primary bg-[#ffffff] text-primary text-sm font-[500] rounded-3xl hover:bg-primary hover:text-[#ffffff] cursor-pointer'>Explore University</button>
                   
                        <a className='text-sm font-bold text-[#0A1C2B]' href={ univercityData[0].university_website}>Website</a>
                        <p className='text-[#7C878E] font-[500] text-md'><i class="fa-solid fa-map-location"></i> View on map</p>
                   </div>
                    </div>

                    <div className='course-detail mt-6'>
                    <h1 className='text-md font-bold'>Key Details</h1>
                        <div className='left w-[100%] mt-2 pl-4 bg-[#E2E2E2] py-4 px-2 rounded-xl'>
                            <div className='key-point flex flex-wrap justify-between items-center'>
                               
                                <div className='icon-box w-1/2 md:w-[25%] flex items-center'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i class="fa-solid fa-graduation-cap"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>University Type</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{univercityData[0].university_type}</p>
                                    </div>
                                </div>

                                <div className='icon-box md:w-[25%] w-1/2 flex items-center'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i class="fa-solid fa-graduation-cap"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Sector</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{univercityData[0].sector}</p>
                                    </div>
                                </div>
                                <div className='icon-box md:w-[25%] w-1/2 flex items-center'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i class="fa-solid fa-calendar-days"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>Total Capacity Across All Providers Locations</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{ univercityData[0].total_students }</p>
                                    </div>
                                </div>
                                <div className='icon-box md:w-[25%] flex items-center'>
                                    <div className='icon text-[#AEAEAE] mr-4'>
                                      <i class="fa-regular fa-timer"></i>
                                    </div>
                                    <div className='text'>
                                        <span className='text-xs text-[#AEAEAE]'>University Id</span>
                                        <p className='text-[#0A1C2B] text-sm font-bold'>{univercityData[0].aa_institute_id}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>


                    <div className='requirement flex justify-between mt-6'>
                        
                        {/* <div className='addinission w-[30%]'>
                            <div className='flex justify-between'>
                              <h1 className='text-md font-bold'>English Requirements</h1>
                              <a className="text-sm text-[#3045C5] border-b border-[#3045C5]">Learn More</a>
                            </div>
                            <div className='border mt-2 border-[#707070] rounded-lg p-2 flex justify-between item-center w-[100%]'>
                                <div  className='border-r border-r-[#707070] w-[25%]'>
                                    <span className='text-xs text-[#7C878E]'>IELTS</span>
                                    <p className='text-md font-bold'>{ univercityData[0].ielts_requirements }</p>
                                </div>
                                <div className='w-[28%] border-r border-r-[#707070]'>
                                    <span className='text-xs text-[#7C878E]'>TOEFL Internet</span>
                                    <p className='text-md font-bold'>{ univercityData[0].toefl_requirements }</p>
                                </div>
                                <div>
                                    <span className='text-xs text-[#7C878E]'>PTE</span>
                                    <p className='text-md font-bold'>{ univercityData[0].pte_requirements }</p>
                                </div>
                            </div>
                        </div> */}
                    </div>


                    <div className='Similar Course mt-10'>
                        <div className='flex items-center'>
                            <div className='flex items-end'>
                              <h1 className='text-lg font-bold mr-4'>Course Offered</h1>
                              <a className='text-[#7C878E] text-xs font-[700] opacity-[.6]'>{totalRec}</a>
                            </div>
                        </div>
                        <div className='grid md:grid-cols-2 grid-cols-1 gap-3 mt-4 relative'>
                           {
                            couseOffered.map((co) => {
                                    return <FeaturedCourse course={co} />
                            })
                           }

                           

                           {loading && <Loader />}
                          
                        </div>
                        <div className="flex justify-center pt-8">
                            <Pagination defaultCurrent={0} total={totalRec} pageSize={6} onChange={(page, pageSize) => setPage(page)} />
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
        <Footer />
    </div>
    )
}

export default Univercity;