import { useState } from "react";
import {Outlet} from 'react-router-dom';
import { useNavigate, NavLink } from "react-router-dom";

const Profile = () => {

    const [profileTabState, setProfileTabState] = useState(1);
    const navigate = useNavigate();
    const [sActive, setSactive] = useState(false);
    

    const renderPersonal = () => {
        navigate('/profile');
        setProfileTabState(1)
    }
    const renderEducation = () => {
        navigate('/profile/education');
        setProfileTabState(2)
    }
    const renderEnglishtest = () => {
        navigate('/profile/english-test');
        setProfileTabState(3)
    }
    const renderAdmissiontest = () => {
        navigate('/profile/admission-test');
        setProfileTabState(4)
    }
    const renderEmployement = () => {
        navigate('/profile/employement');
        setProfileTabState(5)
    }
    const renderDocuments = () => {
        navigate('/profile/documents');
        setProfileTabState(6)
    }



    return (
        <div>
             <div
              className="login-footer 
               w-full
               ">
               <div 
                className="top 
                justify-between
                flex
                w-[full] 
                mx-auto 
              ">

                <div
                  id={sActive ? 'sactive' : ''}
                  className="sidebar transition-all duration-500 pr-6 w-full md:w-1/2 left-[-100%] overflow-hidden fixed lg:static lg:w-[20%] bg-[#DBF9FB] py-8 px-4 h-[100vh]">
                    <div 
                     className="sidebar-inner
                     rounded-xl relative">
                        
                    
                        {sActive ? (
                 <div className="right-4 mb-6">
                 <button className="lg:hidden" onClick={() => setSactive(false)}>
                     <i className="fa fa-times absolute left-[50%] translate-x-[-50%] font-xl"></i>
                 </button>
             </div>
             ) : ''}
                     

                     <div className="w-full flex flex-col">
                        <NavLink to='/profile' onClick={() => setSactive(false)}>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B] profile-nav-btn"
                        ><img src="./assets/images/pbtn.png" className="absolute w-[30px] top-[3px] left-[3px]" /> Personal</button>
                        </NavLink>
                        <NavLink to='/profile/education' onClick={() => setSactive(false)}>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B] profile-nav-btn"
                         >Education</button>
                         </NavLink>
                         <NavLink to='/profile/english-test' onClick={() => setSactive(false)}>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B] profile-nav-btn"
                         >English Test</button>
                         </NavLink>
                         <NavLink to='/profile/admission-test' onClick={() => setSactive(false)}> 
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B] profile-nav-btn"
                         
                         >Admission Tests</button>
                         </NavLink>
                         <NavLink to='/profile/employement' onClick={() => setSactive(false)}>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B] profile-nav-btn"
                         
                         >Employment</button>
                         </NavLink>
                         <NavLink to='/profile/documents' onClick={() => setSactive(false)}>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B] profile-nav-btn"
                         
                         >Documents</button>
                         </NavLink>
                     </div>
                   
                    
                  </div>
                  
                  </div>
            
                 
              <div className="h-[auto] w-full px-4 py-8 xl:pr-[120px] xl:pl-6">

             {!sActive ? (
                 <div className="fixed right-4">
                 <button className="lg:hidden" onClick={() => setSactive(true)}>
                     <i className="fa fa-bars font-xl"></i>
                 </button>
             </div>
             ) : ''}
               
               

                <Outlet />

                {/* <div className="hidden" id={profileTabState === 1 ? 'active-tab-content' : ''}>
                    <Personal />
                </div>

                <div className="hidden" id={profileTabState === 2 ? 'active-tab-content' : ''}>
                    <Education />
                </div>

                <div className="hidden" id={profileTabState === 3 ? 'active-tab-content' : ''}>
                    <Englishtest />
                </div>

                <div className="hidden" id={profileTabState === 4 ? 'active-tab-content' : ''}>
                    <Admissiontest />
                </div>

                <div className="hidden" id={profileTabState === 5 ? 'active-tab-content' : ''}>
                    <Employement />
                </div>

                <div className="hidden" id={profileTabState === 6 ? 'active-tab-content' : ''}>
                    Document
                </div> */}

                 

              </div>




                
               
            </div>

            
            
          
       
       </div>

        </div>
    )
}

export default Profile;