import SetupCard from "../../components/WixardCard/SetupCard";
import { getAreaOfStudy, getCourseLevels, getFieldOfStudy } from "../../Http";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { toast } from 'react-toastify';
import { saveStudyPref } from "../../Http";
import { setStudyPref } from "../../store/authSlice";


const Step2 = ({setStep}) => {

    const [areaOfStudy, setAreaOfStudy] = useState([])
    const [lavelOfStudy, setLavelOfStudy] = useState([]);

    const [saving, setSaving] = useState(false);

    const dispatch = useDispatch()


    const {study_preference} = useSelector(state => state.auth);

    const [stepData, setStepData] = useState({
        education_country: study_preference.education_country,
        field_of_study: study_preference.field_of_study,
        level_of_study: study_preference.level_of_study,
        start_year: study_preference.start_year

    })

    const handleChange = (e) => {
        setStepData({
            ...stepData,
            [e.target.name]: e.target.value
        })
    }

    const handleCheckbox = (e) => {
       if(e.target.checked){
        let arr = [...stepData.education_country, e.target.value];
        setStepData({
            ...stepData,
            education_country: arr
        })
       }else if(!e.target.checked){
        let arr = [...stepData.education_country];
        let index = arr.findIndex((a) => a === e.target.value);
        if(index != -1){
            arr.splice(index, 1)
        }
        setStepData({
            ...stepData,
            education_country: arr
        })
       }

        
    }

    useEffect(() => {
        console.log(stepData)
    }, [stepData])

    const handleNExt = async () => {
        if(!stepData.education_country || !stepData.field_of_study || !stepData.level_of_study || !stepData.start_year){
            return (
                toast.error('Every fields are required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        setSaving(true)

        const { data } = await saveStudyPref(stepData);
        if(data.studyPreference){
            dispatch(setStudyPref({study_preference: data.studyPreference}))
            setStep(3)
        }
        console.log(data)
    }

    

    useEffect(() => {
        setStepData({
            education_country: study_preference.education_country,
            field_of_study: study_preference.field_of_study,
            level_of_study: study_preference.level_of_study,
            start_year: study_preference.start_year
    
        })
    }, [study_preference])

    useEffect(() => {
        if(study_preference.education_country && study_preference.field_of_study && study_preference.level_of_study && study_preference.start_year){
            setStep(3)
        }
    }, [study_preference])


    useEffect(() => {
        const area = async () => {
            const {data} = await getFieldOfStudy();
            setAreaOfStudy(data)
        }
        area()
    }, [])

    useEffect(() => {
        const lavel = async () => {
            const {data} = await getCourseLevels();
            setLavelOfStudy(data)
        }
        lavel()
    }, [])


    return (
        <SetupCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
        <div className="flex w-full justify-between mt-8">
           <div className="w-full">
               <label className="text-xs font-bold">Your dream country</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="aus">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='checkbox' className="check_input hidden" id="aus" value='Australia' onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Australia</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="can">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='checkbox' className="check_input hidden" id="can" value='Canada' onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Canada</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="us">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='checkbox' className="check_input hidden" id="us" value='Usa' onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Usa</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="uk">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='checkbox' className="check_input hidden" id="uk" value="UK" onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">UK</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='education_country' onChange={(e) => handleChange(e)}>
                        <option value='Australia'>Australia</option>
                        <option value="Canada">Canada</option>
                        <option value="Usa">Usa</option>
                </select> */}
           </div>
           
        </div>
        <div className="flex w-full justify-between mt-6">
           <div className="w-[49%]">
               <label className="text-xs font-bold">Prefered field of study</label>
               <select className='wizard_input' name='field_of_study' placeholder="select Gender" onChange={(e) => handleChange(e)}>
                        {
                            areaOfStudy.map((a) => (
                                <option key={a._id} value={a.title}>{a.title}</option>
                            ))
                        }
                </select>
           </div>
           <div className="w-[49%]">
               <label className="text-xs font-bold">Prefered lavel of study</label>
               <select className='wizard_input' name='level_of_study' placeholder="select Gender" onChange={(e) => handleChange(e)}>
               {
                            lavelOfStudy.map((l) => (
                                <option key={l._id} value={l.title}>{l.title}</option>
                            ))
                        }
                </select>
           </div>
           
        </div>

        <div className="flex w-full justify-between mt-6">
           <div className="w-full">
               <label className="text-xs font-bold">When do you want to start study</label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="st">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="start_year" value='1st half 2023' onChange={(e) => handleChange(e)} className="check_input hidden" id="st" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">1<sup>st</sup> Half 2023</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="nd">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="start_year" value='2nd half 2030' className="check_input hidden" id="nd" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">2<sup>nd</sup> Half 2023</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="ny">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="start_year" value='Next Year' onChange={(e) => handleChange(e)} className="check_input hidden" id="ny" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Next Year</span></div>
                </div>
               </div>
               {/* <select className='wizard_input' name='start_year' onChange={(e) => handleChange(e)}>
                        <option value='May 2023'>May 2023</option>
                        <option value="Sept. 2023">Sept. 2023</option>
                        <option value='Next Year'>Next Year</option>
                </select> */}
           </div>
          
           
        </div>
       
       
       
       

        <div className="mt-4">
           <button
           onClick={handleNExt}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
       
   </SetupCard>
    )
}

export default Step2;